/**
 * The interface of solvers for finding solutions to constraint satisfaction problems.
 *
 * @author Takuto Yanagida
 * @version 2023-03-25
 */

class Solver {

	_debug = true;

	/**
	 * The crisp/fuzzy constraint satisfaction problem solved by the solver.
	 */
	_pro;

	/**
	 *  Limit number of iterations.
	 */
	_iterLimit = null;

	/**
	 * Time limit.
	 */
	_timeLimit = null;

	/**
	 * Target 'satisfied constraint rate' or 'constraint satisfaction degree'.
	 */
	_targetDeg = 0.8;

	/**
	 * Listeners of this solver.
	 */
	#listener = [];

	/**
	 * Generates a solver given a constraint satisfaction problem.
	 * @param pro A constraint satisfaction problem.
	 */
	constructor(pro) {
		this._pro = pro;
	}

	/**
	 * Returns the name of the solver.
	 * @return The name.
	 */
	name() {
		return '';
	}

	/**
	 * Placeholder for implementing an algorithm.
	 * The solve method calls this method and returns the return value of this method.
	 * @return True if the algorithm succeeds,
	 */
	exec() {
		return false;
	}

	/**
	 * Sets and limits the maximum number of iterations for the solver's behavior.
	 * After the specified number of iterations, the solver stops as a failure. The specific behavior depends on the solver.
	 * @param count Maximum value; null means not set.
	 */
	setIterationLimit(count = null) {
		this._iterLimit = count;
	}

	/**
	 * Sets a time limit on the solver's behavior.
	 * If the specified time is exceeded, the solver stops as a failure. The specific behavior depends on the solver.
	 * @param msec Time limit. null means not set.
	 */
	setTimeLimit(msec = null) {
		this._timeLimit = msec;
	}

	/**
	 * The goal to be achieved, which is the condition for stopping the solver, is set as the constraint satisfaction degree (fuzzy) or the percentage of constraints satisfied (crisp).
	 * The solver stops as success if the specified percentage is reached or exceeded. The specific behavior depends on the solver.
	 * @param rate Degree or rate. null indicates not set.
	 */
	setTargetRate(rate = null) {
		this._targetDeg = rate;
	}

	/**
	 * Computes the solution to a constraint satisfaction problem.
	 * The specific meaning of the return value depends on the implementation of the algorithm.
	 * @return True if the algorithm succeeds
	 */
	solve() {
		return this.exec();
	}

	addListener(l) {
		this.#listener.add(l);
	}

	removeListener(l) {
		this.#listener.splice(this.#listener.indexOf(l), 1);
	}

	foundSolution(solution, worstDegree) {
		let finish = false;

		for (const l of this.#listener) {
			if (l.foundSolution(solution, worstDegree)) {
				finish = true;
			}
		}
		return finish;
	}

}

/**
 * This class holds the branch pruning states for a domain.
 *
 * @author Takuto Yanagida
 * @version 2023-03-25
 */

class DomainPruner {

	static #UNHIDDEN = -1;

	#hiddenLevels;
	#hiddenSize = 0;

	/**
	 * Generates a class that holds branch pruning states for a domain.
	 * @param size Size of the corresponding domain
	 */
	constructor(size) {
		this.#hiddenLevels = new Array(size);
		this.#hiddenLevels.fill(DomainPruner.#UNHIDDEN);
	}

	/**
	 * Returns the size of the erased element.
	 * @return Size of the erased element.
	 */
	hiddenSize() {
		return this.#hiddenSize;
	}

	/**
	 * Erases the element at the specified index.
	 * @param index Index.
	 * @param level Level.
	 */
	hide(index, level) {
		if (this.#hiddenLevels[index] === DomainPruner.#UNHIDDEN) {
			++this.#hiddenSize;
		}
		this.#hiddenLevels[index] = level;
	}

	/**
	 * Returns whether the element is empty or not.
	 * Returns true if all elements have been erased.
	 * @return True if empty.
	 */
	isEmpty() {
		return this.#hiddenLevels.length === this.#hiddenSize;
	}

	/**
	 * Returns whether or not the element at the specified index has been erased.
	 * @param index Index.
	 * @return True if erased.
	 */
	isValueHidden(index) {
		return this.#hiddenLevels[index] !== DomainPruner.#UNHIDDEN;
	}

	/**
	 * Restores the value that had been erased, by specifying a level.
	 * @param level Level
	 */
	reveal(level) {
		for (let i = 0; i < this.#hiddenLevels.length; i++) {
			if (this.#hiddenLevels[i] === level) {
				this.#hiddenLevels[i] = DomainPruner.#UNHIDDEN;
				--this.#hiddenSize;
			}
		}
	}

	/**
	 * Restores all erased values.
	 */
	revealAll() {
		this.#hiddenLevels.fill(DomainPruner.#UNHIDDEN);
		this.#hiddenSize = 0;
	}

}


/**
 * Class implements a solver using the breakout method.
 * Solves a problem as a maximum CSP.
 *
 * @author Takuto Yanagida
 * @version 2023-03-31
 */

class Breakout extends Solver {

	#weights;

	constructor(p) {
		super(p);
		this.#weights = new Array(this._pro.constraintSize());
		this.#weights.fill(1);
	}

	name() {
		return 'Breakout';
	}

	#findCandidates(vioVars, canList) {
		let maxDiff = 0;

		for (const v of vioVars) {
			const v_val = v.value();  // Save the value
			const v_c   = v.constraints();
			const v_d   = v.domain();

			let nowVio = 0;
			for (const c of v_c) {
				nowVio += (1 - c.isSatisfied()) * this.#weights[c.index()];
			}
			out: for (let i = 0; i < v_d.size(); ++i) {
				const d = v_d.at(i);
				if (v_val === d) continue;
				v.assign(d);
				let diff = nowVio;
				for (const c of v_c) {
					diff -= (1 - c.isSatisfied()) * this.#weights[c.index()];
					// If the improvement is less than the previous improvement, try the next variable.
					if (diff < maxDiff) {
						continue out;
					}
				}
				if (diff > maxDiff) {  // Found assignments that are better than ever before.
					maxDiff = diff;
					canList.clear();
					canList.addVariable(v, d);
				} else if (maxDiff !== 0) {  // Found assignments that can be improved to the same level as before.
					canList.addVariable(v, d);
				}
			}
			v.assign(v_val);  // Restore the value.
		}
	}

	#listViolatingVariables(vioCons) {
		const vvs = new Set();
		for (let i = 0; i < vioCons.length; ++i) {
			const c = vioCons[i];
			for (let j = 0; j < c.size(); ++j) vvs.add(c.at(j));
		}
		return Array.from(vvs);
	}

	exec() {
		const endTime = (this._timeLimit === null) ? Number.MAX_VALUE : (Date.now() + this._timeLimit);
		let iterCount = 0;

		for (const v of this._pro.variables()) {
			if (v.isEmpty()) v.assign(v.domain().at(0));
		}

		const vc      = [];
		const canList = new AssignmentList();

		while (true) {
			this._pro.violatingConstraints(vc);
			if ((this._targetDeg ?? 1) <= this._pro.satisfiedConstraintRate()) {  // Success if violation rate improves from specified
				if (this._debug) console.log('stop: current degree is above the target');
				return true;
			}
			if (this._iterLimit && this._iterLimit < iterCount++) {  // Failure if repeated a specified number
				if (this._debug) console.log('stop: number of iterations has reached the limit');
				return false;
			}
			if (endTime < Date.now()) {  // Failure if time limit is exceeded
				if (this._debug) console.log('stop: time limit has been reached');
				return false;
			}

			if (this._debug) console.log(vc.length + ' violations');
			this.#findCandidates(this.#listViolatingVariables(vc), canList);

			if (0 < canList.size()) {
				const e = canList.arbitraryAssignment();
				e.apply();
				canList.clear();
				if (this._debug) console.log("\t" + e);
			} else {
				for (let i = 0; i < vc.length; ++i) this.#weights[vc[i].index()]++;
				if (this._debug) console.log('breakout');
			}
		}
	}

}

/**
 * This class that implements the forward checking method.
 * The minimum-remaining-values (MRV) heuristic can also be used by specifying the option.
 * Searches for variable assignments that satisfy all constraints and fails if none are found.
 * Each variable must have its own domain because it hides domain elements as branch pruning.
 * Forward checking is also performed for problems with polynomial constraints.
 *
 * @author Takuto Yanagida
 * @version 2023-03-25
 */

class ForwardChecking extends Solver {

	#vars;
	#sol = new AssignmentList();
	#relCons;  // Table to cache constraints between two variables.

	#useMRV = false;

	#iterCount;
	#endTime;

	/**
	 * Generates a solver given a constraint satisfaction problem.
	 * @param p A problem.
	 */
	constructor(p) {
		super(p);
		this.#vars = [...this._pro.variables()];
		for (const v of this.#vars) {
			v.solverObject = new DomainPruner(v.domain().size());
		}
		this.#initializeRelatedConstraintTable();
	}

	name() {
		return 'Forward Checking';
	}

	// Initializes a table that caches constraints between two variables.
	#initializeRelatedConstraintTable() {
		const temp     = [];
		this.#relCons = [];

		for (let j = 0; j < this.#vars.length; ++j) {
			this.#relCons.push(new Array(this.#vars.length));

			for (let i = 0; i < this.#vars.length; ++i) {
				if (i < j) {
					this._pro.constraintsBetween(this.#vars[i], this.#vars[j], temp);
					this.#relCons[j][i] = [...temp];
				}
			}
		}
	}

	// Retrieves an array of constraints from a table that caches constraints between two variables.
	#getConstraintsBetween(i, j) {
		if (i < j) {
			return this.#relCons[j][i];
		}
		return this.#relCons[i][j];
	}

	// Checks for possible assignment to a future variable from the current variable assignment.
	#checkForward(level, currentIndex) {
		for (const v_i of this.#vars) {
			if (!v_i.isEmpty()) continue;  // If it is a past or present variable.
			const d_i  = v_i.domain();
			const dc_i = v_i.solverObject;
			const cs   = this.#getConstraintsBetween(currentIndex, v_i.index());

			for (const c of cs) {
				if (c.emptyVariableSize() !== 1) continue;

				for (let k = 0, n = d_i.size(); k < n; ++k) {
					if (dc_i.isValueHidden(k)) continue;
					v_i.assign(d_i.at(k));

					if (c.isSatisfied() === 0) {  // Do hide when in violation (not even undefined).
						dc_i.hide(k, level);
					}
				}
				v_i.clear();
				if (dc_i.isEmpty()) return false;  // Failure if the domain of one of the future variables is empty.
			}
		}
		return true;
	}

	// Returns the index of the smallest domain variable.
	#indexOfVariableWithMRV() {
		let index = 0;
		let size  = Number.MAX_VALUE;

		for (let i = 0; i < this.#vars.length; ++i) {
			const v = this.#vars[i];
			if (!v.isEmpty()) continue;
			const d = v.domain();
			const s = d.size() - v.solverObject.hiddenSize();
			if (s < size) {
				size  = s;
				index = i;
			}
		}
		return index;
	}

	// Searches for one variable at a time.
	#branch(level) {
		if (this._iterLimit && this._iterLimit < this.#iterCount++) {  // Failure if repeated a specified number.
			if (this._debug) console.log('stop: number of iterations has reached the limit');
			return false;
		}
		if (this.#endTime < Date.now()) {  // Failure if time limit is exceeded.
			if (this._debug) console.log('stop: time limit has been reached');
			return false;
		}

		if (level === this._pro.variableSize()) {
			this.#sol.setProblem(this._pro);
			return true;
		}
		const vc_index = this.#useMRV ? this.#indexOfVariableWithMRV() : level;
		const vc       = this.#vars[vc_index];
		const d        = vc.domain();
		const dc       = vc.solverObject;
		for (let i = 0, n = d.size(); i < n; ++i) {
			if (dc.isValueHidden(i)) continue;
			vc.assign(d.at(i));
			if (this.#checkForward(level, vc_index) && this.#branch(level + 1)) return true;
			for (const v of this.#vars) {
				v.solverObject.reveal(level);
			}
		}
		vc.clear();
		return false;
	}

	// Do search.
	exec() {
		this.#endTime   = (this._timeLimit === null) ? Number.MAX_VALUE : (Date.now() + this._timeLimit);
		this.#iterCount = 0;

		this._pro.clearAllVariables();
		const r = this.#branch(0);

		for (let i = 0; i < this.#sol.size(); ++i) {
			const a = this.#sol.get(i);
			a.apply();
			a.variable().solverObject.revealAll();
		}
		return r;
	}

	/**
	 * The settings made by this method are invalid.
	 */
	setTargetRate() {
		// Do nothing.
	}

	/**
	 * Specify whether to use the minimum-remaining-values (MRV) heuristic.
	 * Use of MRV may increase processing time for some problems.
	 * Default is false.
	 * @param flag Use MRV if true.
	 */
	setUsingMinimumRemainingValuesHeuristics(flag) {
		this.#useMRV = flag;
	}

}

/**
 * This class that implements the forward checking method.
 * Find the solution to the problem as the maximum CSP.
 * Each variable must have its own domain because it hides domain elements as branch pruning.
 *
 * @author Takuto Yanagida
 * @version 2023-03-31
 */

class MaxForwardChecking extends Solver {

	#vars;
	#sol = new AssignmentList();

	#maxVioCount;
	#vioCount;
	#checkedCons = new Set();
	#cons = [];

	#iterCount;
	#endTime;

	constructor(p) {
		super(p);
		this.#vars = [...this._pro.variables()];
		for (const v of this.#vars) {
			v.solverObject = new DomainPruner(v.domain().size());
		}
		this.#maxVioCount = this._pro.constraintSize();
	}

	name() {
		return 'Forward Checking for Max CSPs';
	}

	#branch(level, vioCount) {
		if (this._iterLimit && this._iterLimit < this.#iterCount++) return false;  // Failure if repeated a specified number.
		if (this.#endTime < Date.now()) return false;  // Failure if time limit is exceeded.

		if (level === this._pro.variableSize()) {
			const vcs = this._pro.violatingConstraintSize();
			if (vcs < this.#maxVioCount) {
				this.#maxVioCount = vcs;
				this.#sol.setProblem(this._pro);
				if (this._debug) console.log(`   refreshed ${this.#maxVioCount}`);
				if ((this._targetDeg ?? 1) <= this._pro.satisfiedConstraintRate()) return true;
			}
			return false;
		}
		const vc  = this.#vars[level];
		const dom = vc.domain();
		const dc  = vc.solverObject;

		for (let i = 0; i < dom.size(); ++i) {
			if (dc.isValueHidden(i)) continue;
			vc.assign(dom.at(i));
			this.#vioCount = vioCount + this.#getAdditionalViolationCount(level, vc);  // for max begin
			if (this.#vioCount > this.#maxVioCount) continue;  // for max end
			if (this.#checkForward(level) && this.#branch(level + 1, this.#vioCount)) return true;
			for (const v of this.#vars) {
				v.solverObject.reveal(level);
			}
		}
		vc.clear();
		return false;
	}

	// Checks for possible assignment to a future variable from the current variable assignment.
	#checkForward(level) {
		const vc = this.#vars[level];

		for (let i = level + 1; i < this.#vars.length; ++i) {
			const future = this.#vars[i];
			this._pro.constraintsBetween(vc, future, this.#cons);

			for (const c of this.#cons) {
				if (c.emptyVariableSize() !== 1) continue;
				if (this.#revise(future, c, level)) {
					if (future.solverObject.isEmpty()) return false;  // Failure if the domain of one of the future variables is empty.
				}
			}
		}
		return true;
	}

	// Find the number of constraint violations that have increased due to the current value of the variable vc.
	#getAdditionalViolationCount(level, vc) {
		let avc = 0;
		this.#checkedCons.clear();  // Reuse.
		for (let i = 0; i < level; ++i) {
			this._pro.constraintsBetween(vc, this.#vars[i], this.#cons);

			for (const c of this.#cons) {
				if (this.#checkedCons.has(c)) continue;  // Because of the possibility of duplication in polynomial constraints
				if (c.isSatisfied() === 0) ++avc;  // Neither satisfied nor undefined.
				this.#checkedCons.add(c);
			}
		}
		return avc;
	}

	// Remove values from the domain of v1 that do not correspond to v2. That is, match v1 with v2.
	#revise(v1, c, level) {
		let deleted = false;

		const dom = v1.domain();
		const dc  = v1.solverObject;

		for (let i = 0; i < dom.size(); ++i) {
			if (dc.isValueHidden(i)) continue;
			v1.assign(dom.at(i));

			if (c.isSatisfied() === 0 && this.#vioCount + 1 > this.#maxVioCount) {
				dc.hide(i, level);
				deleted = true;
			}
		}
		return deleted;
	}

	exec() {
		this.#endTime   = (this._timeLimit === null) ? Number.MAX_VALUE : (Date.now() + this._timeLimit);
		this.#iterCount = 0;

		this._pro.clearAllVariables();
		const r = this.#branch(0, 0);
		if (this._debug) {
			if (r) {
				console.log('stop: current degree is above the target');
			} else {
				if (this._iterLimit && this._iterLimit < this.#iterCount) {
					console.log('stop: number of iterations has reached the limit');
				}
				if (this.#endTime < Date.now()) {
					console.log('stop: time limit has been reached');
				}
			}
		}

		for (let i = 0; i < this.#sol.size(); ++i) {
			const a = this.#sol.get(i);
			a.apply();
			a.variable().solverObject.revealAll();
		}
		return r;
	}

}

/**
 * This class implements GENET.
 * CSP (but only Binary CSP) is supported.
 * Find the solution to the problem as the maximum CSP.
 *
 * @author Takuto Yanagida
 * @version 2023-03-31
 */

class GENET extends Solver {

	static nextInt(max) {
		return Math.floor(Math.random() * Math.floor(max));
	}

	#clusters = [];
	#connections;

	constructor(p) {
		super(p);
		if (!this.#createNetwork()) {
			throw new Exception();
		}
	}

	name() {
		return 'GENET';
	}

	#createNetwork() {
		if (this._debug) console.log('network creation start');
		const cons = [];

		for (const v of this._pro.variables()) {
			if (v.domain().size() === 0) return false;
			this.#clusters.push(new GENET.Cluster(v));
		}
		for (const c of this._pro.constraints()) {
			if (c.size() === 1) {  // In the case of unary constraints.
				const v  = c.at(0);
				const cl = this.#clusters[c.at(0).index()];

				for (let i = 0; i < cl.size(); ++i) {
					const origVal = v.value();  // Save the value.
					v.assign(cl.get(i)._value);

					if (c.isSatisfied() === 0) {
						cons.push(new GENET.Connection(cl.get(i)));
					}
					v.assign(origVal);  // Restore the value.
				}
			} else {  // In the case of binary constraints.
				const v1   = c.at(0);
				const v2   = c.at(1);
				const cl_f = this.#clusters[c.at(0).index()];
				const cl_s = this.#clusters[c.at(1).index()];

				for (let i = 0; i < cl_f.size(); ++i) {
					const origVal1 = v1.value();  // Save the value.
					v1.assign(cl_f.get(i)._value);

					for (let j = 0; j < cl_s.size(); ++j) {
						const origVal2 = v2.value();  // Save the value.
						v2.assign(cl_s.get(j)._value);

						if (c.isSatisfied() === 0) {
							cons.push(new GENET.Connection(cl_f.get(i), cl_s.get(j)));
						}
						v2.assign(origVal2);  // Restore the value.
					}
					v1.assign(origVal1);  // Restore the value.
				}
			}
		}
		for (const cl of this.#clusters) {
			for (const n of cl._neurons) n.lockConnections();
		}
		this.#connections = cons;
		if (this._debug) console.log('network creation complete');
		return true;
	}

	#shuffle(is) {
		for (let i = is.length; i > 1; --i) {
			const j    = GENET.nextInt(i);
			const temp = is[i - 1];
			is[i - 1] = is[j];
			is[j]     = temp;
		}
		return is;
	}

	exec() {
		const endTime = (this._timeLimit === null) ? Number.MAX_VALUE : (Date.now() + this._timeLimit);
		let iterCount = 0;

		const sol   = new AssignmentList();
		const order = [];
		for (let i = 0; i < this.#clusters.length; i++) {
			order.push(i);
		}

		let cur = this._pro.satisfiedConstraintRate();

		while (true) {
			if (this._iterLimit && this._iterLimit < iterCount++) {  // Failure if repeated a specified number
				if (this._debug) console.log('stop: number of iterations has reached the limit');
				break;
			}
			if (endTime < Date.now()) {  // Failure if time limit is exceeded
				if (this._debug) console.log('stop: time limit has been reached');
				break;
			}

			let modified = false;
			for (const i of this.#shuffle(order)) {
				if (this.#clusters[i].setActivityMaximumInput()) {
					modified = true;  // Turn on the node with the largest input in each cluster
				}
			}
			if (!modified) {  // When the local minimum solution is reached.
				for (const con of this.#connections) {
					con.refreshWeight();  // Update weights for all connections
				}
			} else {
				for (const clu of this.#clusters) {
					clu.applyToVariable();  // Apply to variable
				}
				const d = this._pro.satisfiedConstraintRate();
				if (cur < d) {  // If it's a better assignment than ever, save it.
					cur = d;
					if (this._debug) console.log(`satisfied constraint rate: ${d}`);
					sol.setProblem(this._pro);
					if (this._targetDeg ?? 1 <= cur) {  // Success if violation rate improves from specified
						if (this._debug) console.log('stop: current degree is above the target');
						sol.apply();
						return true;
					}
				}
			}
		}
		sol.apply();  // Despite the failures, the best assignment so far is applied for now.
		return false;
	}

}

{

	class Cluster {

		static nextInt(max) {
			return Math.floor(Math.random() * Math.floor(max));
		}

		#var;
		#index;
		#maxNeurons = [];
		_neurons;

		constructor(v) {
			this.#var = v;

			const d = v.domain();
			this._neurons = [];

			for (let i = 0; i < d.size(); ++i) {
				this._neurons.push(new Neuron(d.at(i)));
			}
			this.#setActivity(Cluster.nextInt(this._neurons.length));
		}

		#setActivity(index) {
			for (const n of this._neurons) {
				n._isActive = false;
			}
			this._neurons[index]._isActive = true;
			this.#index = index;
		}

		applyToVariable() {
			this.#var.assign(this._neurons[this.#index]._value);
		}

		get(index) {
			return this._neurons[index];
		}

		neurons() {
			return this._neurons;
		}

		// Turn on the node with the largest input.
		setActivityMaximumInput() {
			this.#maxNeurons.length = 0;

			let max       = Number.NEGATIVE_INFINITY;
			let alreadyOn = false;

			for (let i = 0; i < this._neurons.length; ++i) {
				const input = this._neurons[i].getInput();

				if (max <= input) {
					if (max < input) {
						max = input;
						this.#maxNeurons.length = 0;
						alreadyOn = false;
					}
					this.#maxNeurons.push(i);
					if (this.#index === i) {
						alreadyOn = true;
					}
				}
			}
			if (alreadyOn || this.#maxNeurons.length === 0) {
				return false;
			}
			this.#setActivity(this.#maxNeurons[Cluster.nextInt(this.#maxNeurons.length)]);
			return true;
		}

		size() {
			return this._neurons.length;
		}

	}
	GENET.Cluster = Cluster;

	class Connection {

		#first;
		#second;
		_weight;  // Direct reference (read) allowed.

		// Order of neurons must be the same as the order of variables that the constraint has.
		constructor(first, second = null) {
			this._weight = -1;

			this.#first = first;
			this.#first.addConnection(this);
			this.#second = second;
			if (this.#second !== null) {
				this.#second.addConnection(this);
			}
		}

		getNeuron(self) {
			if (self === this.#first)  return this.#second;
			if (self === this.#second) return this.#first;
			return null;
		}

		refreshWeight() {
			if (!this.#first._isActive || (this.#second !== null && !this.#second._isActive)) {
				return;
			}
			this._weight += -1;
		}

	}
	GENET.Connection = Connection;

	class Neuron {

		#conTemp = [];
		#connections;
		_value;  // Direct reference (read) allowed.
		_isActive = false;  // Direct reference (read, write) allowed.

		constructor(value) {
			this._value = value;
		}

		addConnection(c) {
			this.#conTemp.push(c);
		}

		lockConnections() {
			this.#connections = [...this.#conTemp];
			this.#conTemp     = null;  // No longer being used.
		}

		getInput() {
			let ret = 0;
			for (const c of this.#connections) {
				const n = c.getNeuron(this);  // If n is null, then the unary constraint.
				ret += c._weight * ((n === null || n._isActive) ? 1 : 0);
			}
			return ret;
		}

	}
	GENET.Neuron = Neuron;

}

/**
 * Class implements the local changes method.
 *
 * @author Takuto Yanagida
 * @version 2023-03-31
 */

class LocalChanges extends Solver {

	static #setPlusSet(s1, s2) {
		const sn = new Set(s1);
		for (const v of s2) sn.add(v);
		return sn;
	}

	static #setMinusSet(s1, s2) {
		const sn = new Set(s1);
		for (const v of s2) sn.delete(v);
		return sn;
	}

	static #setPlusElement(s, e) {
		const sn = new Set(s);
		sn.add(e);
		return sn;
	}

	static #setMinusElement(s, e) {
		const sn = new Set(s);
		sn.delete(e);
		return sn;
	}

	#iterCount;
	#endTime;
	#globalReturn;

	constructor(p, unassignAll = false) {
		super(p);
		if (unassignAll) {
			this._pro.clearAllVariables();
		}
	}

	name() {
		return 'Local Changes';
	}

	#createNewV3(V1_V2, v, val) {
		const newV3 = new Set();
		const cs    = new Set();
		const temp  = [];

		for (const va of V1_V2) {
			this._pro.constraintsBetween(v, va, temp);
			for (const c of temp) cs.add(c);
		}
		const origVal = v.value();  // Save the value.
		v.assign(val);

		for (const c of cs) {
			if (c.isSatisfied() === 0) {
				for (let i = 0; i < c.size(); ++i) {
					newV3.add(c.at(i));
				}
			}
		}
		v.assign(origVal);  // Restore the value.
		newV3.delete(v);
		return newV3;
	}

	#isConsistent(A, v, val) {
		const cs   = new Set();
		const temp = [];

		for (const va of A) {
			this._pro.constraintsBetween(v, va, temp);
			for (const c of temp) cs.add(c);
		}
		const origVal = v.value();  // Save the value.
		v.assign(val);

		for (const c of cs) {
			if (c.isSatisfied() === 0) {
				v.assign(origVal);  // Restore the value.
				return false;
			}
		}
		v.assign(origVal);  // Restore the value.
		return true;
	}

	#lcValue(V1, V2, v, val) {
		if (!this.#isConsistent(V1, v, val)) {
			return false;
		}
		const V1_V2 = LocalChanges.#setPlusSet(V1, V2);
		if (this.#isConsistent(V1_V2, v, val)) {
			return true;
		}
		const V3 = this.#createNewV3(V1_V2, v, val);

		const T = LocalChanges.#setMinusSet(V1_V2, V3);
		if (!this.#isConsistent(T, v, val)) {
			if (this._debug) console.log('bug');
		}

		for (const vv of V3) {
			vv.clear();
		}
		V1 = LocalChanges.#setPlusElement(V1, v);
		V2 = LocalChanges.#setMinusSet(V2, V3);
		return this.#lcVariables(V1, V2, V3);
	}

	#lcVariable(V1, V2, v, d) {
		if (d.size === 0) {
			return false;
		}
		const val = d.values().next().value;
		const  al = AssignmentList.fromVariables(V2);
		v.assign(val);

		const ret = this.#lcValue(V1, V2, v, val);
		if (ret || this.#globalReturn) {
			return ret;
		}

		v.clear();
		al.apply();

		return this.#lcVariable(V1, V2, v, LocalChanges.#setMinusElement(d, val));
	}

	#lcVariables(V1, V2, V3) {
		if (this._debug) {
			console.log(`V1 ${V1.size}, V2' ${V2.size}, V3' ${V3.size}`);
		}
		if ((this._targetDeg ?? 1) <= this._pro.satisfiedConstraintRate()) {  // Success if violation rate improves from specified
			if (this._debug) console.log('stop: current degree is above the target');
			this.#globalReturn = true;
			return true;
		}
		if (this._iterLimit && this._iterLimit < this.#iterCount++) {  // Failure if repeated a specified number
			if (this._debug) console.log('stop: number of iterations has reached the limit');
			this.#globalReturn = true;
			return false;
		}
		if (this.#endTime < Date.now()) {  // Failure if time limit is exceeded
			if (this._debug) console.log('stop: time limit has been reached');
			this.#globalReturn = true;
			return false;
		}

		if (V3.size === 0) {
			return true;
		}
		const v    = V3.values().next().value;
		const dom  = v.domain();
		const d    = new Set();
		for (let i = 0; i < dom.size(); ++i) {
			d.add(dom.at(i));
		}

		const ret = this.#lcVariable(V1, V2, v, d);
		if (!ret || this.#globalReturn) {
			return ret;
		}
		V2 = LocalChanges.#setPlusElement(V2, v);
		V3 = LocalChanges.#setMinusElement(V3, v);
		return this.#lcVariables(V1, V2, V3);
	}

	exec() {
		this.#endTime      = (this._timeLimit === null) ? Number.MAX_VALUE : (Date.now() + this._timeLimit);
		this.#iterCount    = 0;
		this.#globalReturn = false;

		if (this._pro.emptyVariableSize() === 0) {
			this._pro.clearAllVariables();
		}
		const notFixed   = new Set();
		const unassigned = new Set();
		for (const v of this._pro.variables()) {
			(!v.isEmpty() ? notFixed : unassigned).add(v);
		}
		return this.#lcVariables(new Set(), notFixed, unassigned);
	}

}

/**
 * Class implements the local changes method.
 * The implementation is optimized by converting recursive calls to loops.
 *
 * @author Takuto Yanagida
 * @version 2023-03-31
 */

class LocalChangesEx extends Solver {

	static #setPlusSet(s1, s2) {
		const sn = new Set(s1);
		for (const v of s2) sn.add(v);
		return sn;
	}

	static #setMinusSet(s1, s2) {
		const sn = new Set(s1);
		for (const v of s2) sn.delete(v);
		return sn;
	}

	static #setPlusElement(s, e) {
		const sn = new Set(s);
		sn.add(e);
		return sn;
	}

	static #setMinusElement(s, e) {
		const sn = new Set(s);
		sn.delete(e);
		return sn;
	}

	#iterCount;
	#endTime;
	#globalReturn;

	constructor(p, unassignAll = false) {
		super(p);
		if (unassignAll) {
			this._pro.clearAllVariables();
		}
	}

	name() {
		return 'Local Changes Ex';
	}

	#createNewV3(V1_V2, v, val) {
		const newV3 = new Set();
		const cs    = new Set();
		const temp  = [];

		for (const va of V1_V2) {
			this._pro.constraintsBetween(v, va, temp);
			for (const c of temp) cs.add(c);
		}
		const origVal = v.value();  // Save the value.
		v.assign(val);

		for (const c of cs) {
			if (c.isSatisfied() === 0) {
				for (let i = 0; i < c.size(); ++i) {
					newV3.add(c.at(i));
				}
			}
		}
		v.assign(origVal);  // Restore the value.
		newV3.delete(v);
		return newV3;
	}

	#isConsistent(A, v, val) {
		const cs   = new Set();
		const temp = [];

		for (const va of A) {
			this._pro.constraintsBetween(v, va, temp);
			for (const c of temp) cs.add(c);
		}
		const origVal = v.value();  // Save the value.
		v.assign(val);

		for (const c of cs) {
			if (c.isSatisfied() === 0) {
				v.assign(origVal);  // Restore the value.
				return false;
			}
		}
		v.assign(origVal);  // Restore the value.
		return true;
	}

	#lcValue(V1, V2, v) {
		if (!this.#isConsistent(V1, v, v.value())) {
			return false;
		}
		const V1_V2 = LocalChangesEx.#setPlusSet(V1, V2);
		if (this.#isConsistent(V1_V2, v, v.value())) {
			return true;
		}
		const V3 = this.#createNewV3(V1_V2, v, v.value());

		V2 = LocalChangesEx.#setMinusSet(V2, V3);
		V1 = LocalChangesEx.#setPlusElement(V1, v);
		return this.#lcVariables(V1, V2, V3);
	}

	#lcVariable(V1, V2, v) {
		const dom = v.domain();
		for (let i = 0; i < dom.size(); ++i) {
			const val = dom.at(i);
			const s   = AssignmentList.fromVariables(V2);
			v.assign(val);

			const ret = this.#lcValue(V1, V2, v);
			if (ret || this.#globalReturn) {
				return ret;
			}

			v.clear();
			s.apply();
		}
		return false;
	}

	#lcVariables(V1, V2, V3) {
		V2 = new Set(V2);  // Clone
		V3 = new Set(V3);  // Clone

		while (true) {
			if (this._debug) {
				console.log(`V1 ${V1.size}, V2' ${V2.size}, V3' ${V3.size}`);
			}
			if ((this._targetDeg ?? 1) <= this._pro.satisfiedConstraintRate()) {  // Success if violation rate improves from specified
				if (this._debug) console.log('stop: current degree is above the target');
				this.#globalReturn = true;
				return true;
			}
			if (this._iterLimit && this._iterLimit < this.#iterCount++) {  // Failure if repeated a specified number
				if (this._debug) console.log('stop: number of iterations has reached the limit');
				this.#globalReturn = true;
				return false;
			}
			if (this.#endTime < Date.now()) {  // Failure if time limit is exceeded
				if (this._debug) console.log('stop: time limit has been reached');
				this.#globalReturn = true;
				return false;
			}

			if (V3.size === 0) {
				return true;
			}
			const v   = V3.values().next().value;
			const ret = this.#lcVariable(V1, V2, v);

			if (!ret || this.#globalReturn) {
				return ret;
			}
			V2.add(v);
			V3.delete(v);
		}
	}

	exec() {
		this.#endTime      = (this._timeLimit === null) ? Number.MAX_VALUE : (Date.now() + this._timeLimit);
		this.#iterCount    = 0;
		this.#globalReturn = false;

		if (this._pro.emptyVariableSize() === 0) {
			this._pro.clearAllVariables();
		}
		const notFixed   = new Set();
		const unassigned = new Set();
		for (const v of this._pro.variables()) {
			(!v.isEmpty() ? notFixed : unassigned).add(v);
		}
		return this.#lcVariables(new Set(), notFixed, unassigned);
	}

}

/**
 * This class implements the SRS algorithm for crisp CSP.
 * The given crisp CSP is treated as the maximum CSP.
 * Similar to SRS 3, the repair algorithm searches for an assignment that satisfies itself without reducing the number of satisfactions of its neighbors.
 *
 * @author Takuto Yanagida
 * @version 2023-03-31
 */

class CrispSRS3 extends Solver {

	#closedList          = new Set();
	#openList            = new Set();  // LinkedHashSet is used in the original implementation.
	#nodes               = [];
	#neighborConstraints = [];  // Cache

	#isRandom = true;

	constructor(p) {
		super(p);
		for (const c of this._pro.constraints()) {
			this.#nodes.push(new CrispSRS3.TreeNode(c));
			this.#neighborConstraints.push(null);
		}
	}

	name() {
		return 'SRS 3 for Crisp CSPs';
	}

	#getNeighborConstraints(c) {
		const index = c.index();

		if (this.#neighborConstraints[index] === null) {
			this.#neighborConstraints[index] = c.neighbors([]);
		}
		return this.#neighborConstraints[index];
	}

	#repair(c0) {
		if (this._debug) console.log('repair');

		const candidates = new AssignmentList();
		let maxDiff = 0;

		for (let i = 0; i < c0.size(); ++i) {
			const v     = c0.at(i);
			const v_val = v.value();  // Save the value
			const v_c   = v.constraints();
			const v_d   = v.domain();

			let nowVio = 0;
			for (const c of v_c) {
				nowVio += (1 - c.isSatisfied());
			}
			out: for (let j = 0; j < v_d.size(); ++j) {
				const d = v_d.at(j);
				if (v_val === d) continue;
				v.assign(d);
				if (c0.isSatisfied() !== 1) continue;  // Assuming c0 improvement

				let diff = nowVio;
				for (const n of v_c) {
					diff -= (1 - n.isSatisfied());
					if (diff < maxDiff) continue out;  // If the improvement is less than the previous improvement, try the next variable.
				}
				if (diff > maxDiff) {  // An assignment that are better than ever before is found.
					maxDiff = diff;
					candidates.clear();
					candidates.addVariable(v, d);
				} else if (maxDiff !== 0) {  // An assignments that can be improved to the same level as before is found.
					candidates.addVariable(v, d);
				}
			}
			v.assign(v_val);  // Restore the value
		}
		if (candidates.size() > 0) {
			const e = this.#isRandom ? candidates.arbitraryAssignment() : candidates.get(0);
			e.apply();
			if (this._debug) console.log('\t' + e);
			return true;
		}
		return false;
	}

	#shrink(node, c_stars) {
		const temp = [];
		let cur = node;

		while (true) {  // This procedure is originally a recursive call, but converted to a loop
			cur = cur.parent();
			temp.length = 0;
			cur.getDescendants(temp);
			cur.clear();

			for (const n of c_stars) {
				this.#openList.delete(n);
				this.#closedList.delete(n);
			}

			if (c_stars.delete(cur)) {
				break;
			}
			this.#openList.add(cur);
			if (cur.parent() !== null && !this.#repair(cur.parent().getObject())) {
				break;
			}
		}
	}

	#spread(node) {
		if (this._debug) console.log('spread');
		this.#closedList.add(node);

		for (const c of this.#getNeighborConstraints(node.getObject())) {
			const tnc = this.#nodes[c.index()];

			if (!this.#closedList.has(tnc) && !this.#openList.has(tnc)) {  // For constraints that are not included in Open or Closed
				tnc.clear();  // Because of its reuse, it may have had children when it was used before.
				node.add(tnc);
				this.#openList.add(tnc);
			}
		}
	}

	#srs(c_stars) {
		if (this._debug) console.log('srs');
		const endTime = (this._timeLimit === null) ? Number.MAX_VALUE : (Date.now() + this._timeLimit);
		let iterCount = 0;

		this.#closedList.clear();
		this.#openList.clear();
		for (const n of c_stars) {
			this.#openList.add(n);
		}

		while (c_stars.size && this.#openList.size) {
			if ((this._targetDeg ?? 1) <= this._pro.satisfiedConstraintRate()) {  // Success if violation rate improves from specified
				if (this._debug) console.log('stop: current degree is above the target');
				return true;
			}
			if (this._iterLimit && this._iterLimit < iterCount++) {  // Failure if repeated a specified number
				if (this._debug) console.log('stop: number of iterations has reached the limit');
				return false;
			}
			if (endTime < Date.now()) {  // Failure if time limit is exceeded
				if (this._debug) console.log('stop: time limit has been reached');
				return false;
			}

			const node = this.#openList.values().next().value;
			this.#openList.delete(node);

			if (this.#repair(node.getObject())) {
				if (!c_stars.delete(node)) {  // If the repaired node is included in C* (to be deleted)
					if (node.parent() !== null && this.#repair(node.parent().getObject())) {  // When its improvement leads to the improvement of its parents
						this.#shrink(node, c_stars);
					} else {
						this.#spread(node);
					}
				}
			} else {  // In case of repair failure
				this.#spread(node);
			}
		}
		return false;
	}

	exec() {
		const vcs     = this._pro.violatingConstraints([]);
		const c_stars = new Set();

		for (const c of vcs) {
			const tnc = this.#nodes[c.index()];
			c_stars.add(tnc);
		}
		if (this.#srs(c_stars)) {
			return true;
		}
		return c_stars.length === 0;
	}

	/**
	 * Sets the randomness of the algorithm.
	 * Enabling randomness reduces the risk of falling into a local solution, but makes the solution unrepeatable.
	 * @param flag If true, randomness is enabled.
	 */
	setRandomness(flag) {
		this.#isRandom = flag;
	}

}

{

	class TreeNode {

		#children = [];
		#parent;
		#obj;

		constructor(obj) {
			this.#obj = obj;
		}

		add(tn) {
			tn.#parent = this;
			this.#children.push(tn);
		}

		clear() {
			for (const tn of this.#children) {
				tn.#parent = null;
			}
			this.#children.length = 0;
		}

		getDescendants(tns) {
			tns.push(this);

			for (const tn of this.#children) {
				tn.getDescendants(tns);
			}
		}

		getObject() {
			return this.#obj;
		}

		parent() {
			return this.#parent;
		}

	}
	CrispSRS3.TreeNode = TreeNode;

}


/**
 * Class implements a solver using the breakout method for fuzzy CSP.
 *
 * @author Takuto Yanagida
 * @version 2023-03-25
 */

class FuzzyBreakout extends Solver {

	#weights;
	#lastSolDeg;

	#isRandom = true;

	constructor(p) {
		super(p);
		this.#weights = new Array(this._pro.constraintSize());
		this.#weights.fill(1);
	}

	name() {
		return 'Fuzzy Breakout';
	}

	foundSolution() {
		return false;
	}

	#findCandidates(worstVars, canList) {
		let maxDiff = 0;

		for (const v of worstVars) {
			const v_val = v.value();  // Save the value
			const v_c   = v.constraints();
			const v_d   = v.domain();

			let nowVio = 0;
			for (const c of v_c) {
				nowVio += (1 - c.satisfactionDegree()) * this.#weights[c.index()];
			}
			out: for (let i = 0; i < v_d.size(); ++i) {
				const d = v_d.at(i);
				if (v_val === d) continue;
				v.assign(d);
				let diff = nowVio;
				for (const c of v_c) {
					diff -= (1 - c.satisfactionDegree()) * this.#weights[c.index()];
					// If the improvement is less than the previous improvement, try the next variable.
					if (diff < maxDiff) {
						continue out;
					}
				}
				if (diff > maxDiff) {  // Found assignments that are better than ever before.
					maxDiff = diff;
					canList.clear();
					canList.addVariable(v, d);
				} else if (maxDiff !== 0) {  // Found assignments that can be improved to the same level as before.
					canList.addVariable(v, d);
				}
			}
			v.assign(v_val);  // Restore the value.
		}
	}

	#listWorstVariables(worstCons) {
		const wvs = new Set();
		for (let i = 0; i < worstCons.length; ++i) {
			const c = worstCons[i];
			for (let j = 0; j < c.size(); ++j) wvs.add(c.at(j));
		}
		return Array.from(wvs);
	}

	exec() {
		const endTime = (this._timeLimit === null) ? Number.MAX_VALUE : (Date.now() + this._timeLimit);
		let iterCount = 0;

		for (const v of this._pro.variables()) {
			if (v.isEmpty()) v.assign(v.domain().at(0));
		}

		const deg     = this._pro.worstSatisfactionDegree();
		const vc      = [];
		const canList = new AssignmentList();

		const sol = new AssignmentList();

		while (true) {
			const wsd = this._pro.constraintsWithWorstSatisfactionDegree(vc);
			if (this._targetDeg !== null && this._targetDeg <= wsd) {  // Success if the degree improves from specified
				if (this._debug) console.log('stop: current degree is above the target');
				return true;
			}
			if (this._iterLimit && this._iterLimit < iterCount++) {  // Failure if repeated a specified number
				if (this._debug) console.log('stop: number of iterations has reached the limit');
				break;
			}
			if (endTime < Date.now()) {  // Failure if time limit is exceeded
				if (this._debug) console.log('stop: time limit has been reached');
				break;
			}

			if (this._debug) console.log('worst satisfaction degree: ' + wsd);

			if (this.#lastSolDeg < wsd) {
				sol.setProblem(this._pro);
				this.#lastSolDeg = wsd;
				if (foundSolution(sol, this.#lastSolDeg)) {  // Call hook
					return true;
				}
			}

			this.#findCandidates(this.#listWorstVariables(vc), canList);

			if (0 < canList.size()) {
				const e = this.#isRandom ? canList.arbitraryAssignment() : canList.get(0);
				e.apply();
				canList.clear();
				if (this._debug) console.log("\t" + e);
			} else {
				for (let i = 0; i < vc.length; ++i) this.#weights[vc[i].index()]++;
				if (this._debug) console.log("breakout");
			}
		}
		if (this._targetDeg === null && deg < this._pro.worstSatisfactionDegree()) return true;
		return false;
	}

	/**
	 * Sets the randomness of the algorithm.
	 * Enabling randomness reduces the risk of local solutions, but makes the solution unrepeatable.
	 * @param flag Whether the randomness is enabled.
	 */
	setRandomness(flag) {
		this.#isRandom = flag;
	}

}

/**
 * This class implements the forward checking method for fuzzy CSP.
 * The minimum-remaining-values (MRV) heuristic can also be used by specifying the option.
 * Each variable must have its own domain because it hides domain elements as branch pruning.
 * Forward checking is also performed for problems with polynomial constraints.
 *
 * @author Takuto Yanagida
 * @version 2023-03-25
 */

class FuzzyForwardChecking extends Solver {

	static CONTINUE  = 0;
	static TERMINATE = 1;

	#vars;
	#sol = new AssignmentList();
	#relCons;  // Table to cache constraints between two variables.

	#solWorstDeg = 0;  // Degree of existing solutions (no need to find a solution less than this).

	#iterCount;
	#endTime;
	#useMRV = false;
	#degInc = 0;

	#sequence;
	#unaryCons;
	#checkedCons;
	#pruneIntensively = false;

	/**
	 * Generates the solver given a fuzzy constraint satisfaction problem.
	 * @param p A fuzzy problem.
	 * @param worstSatisfactionDegree Worst satisfaction degree.
	 */
	constructor(p, worstSatisfactionDegree = null) {
		super(p);
		this.#vars = [...this._pro.variables()];
		this.#sequence = new Array(this._pro.variableSize());
		this.#initializeRelatedConstraintTable();
		this.#checkedCons = new Array(this._pro.constraintSize());

		const temp = [];
		for (const c of this._pro.constraints()) {
			if (c.size() === 1) temp.push(c);
		}
		this.#unaryCons = [...temp];  // To make it even if it is empty.
		if (worstSatisfactionDegree) {
			this.#solWorstDeg = worstSatisfactionDegree;
		}
	}

	name() {
		return 'Forward Checking for Fuzzy CSPs';
	}

	// Initializes a table that caches constraints between two variables.
	#initializeRelatedConstraintTable() {
		const temp = [];
		this.#relCons = [];

		for (let j = 0; j < this.#vars.length; ++j) {
			this.#relCons.push(new Array(this.#vars.length));

			for (let i = 0; i < this.#vars.length; i++) {
				if (i < j) {
					this._pro.constraintsBetween(this.#vars[i], this.#vars[j], temp);
					this.#relCons[j][i] = [...temp];
				}
			}
		}
	}

	// Retrieves an array of constraints from a table that caches constraints between two variables.
	#getConstraintsBetween(vi_index, vj_index) {
		if (vi_index < vj_index) {
			return this.#relCons[vj_index][vi_index];
		}
		return this.#relCons[vi_index][vj_index];
	}

	// Prune elements of the domain that make the unary constraint worse than the current worst degree.
	#pruneUnaryConstraints() {
		for (const c of this.#unaryCons) {
			const v      = c.at(0);
			const orgVal = v.value();  // Save the value.
			const d      = v.domain();
			const dc     = v.solverObject;

			for (let i = 0, n = d.size(); i < n; ++i) {
				v.assign(d.at(i));
				if (c.satisfactionDegree() <= this.#solWorstDeg) {
					dc.hide(i, -1);  // Here's a branch pruning!
				}
			}
			v.assign(orgVal);  // Restore the value.
			if (dc.isEmpty()) return false;
		}
		return true;
	}

	// Check for consistency between the current variable and one future variable, and prune elements of the domain that are inconsistent (when there is one unassigned variable in the scope of the constraint).
	#checkForwardConsistency(level, vi, c) {
		const di  = vi.domain();
		const dci = vi.solverObject;

		for (let i = 0, n = di.size(); i < n; ++i) {
			if (dci.isValueHidden(i)) continue;
			vi.assign(di.at(i));
			if (c.satisfactionDegree() <= this.#solWorstDeg) {  // It is not a solution when it is 'smaller than or equals'.
				dci.hide(i, level);  // Here's a branch pruning!
			}
		}
		vi.clear();
		return !dci.isEmpty();  // Succeeds if the domain di of the future variable vi is not empty.
	}

	// Check for consistency between the current variable and one future variable, and prune elements of the domain that are inconsistent (when there are two unassigned variables in the scope of the constraint).
	#checkForwardConsistency2(level, vi, c) {
		const di  = vi.domain();
		const dci = vi.solverObject;
		const vj  = null;

		for (let i = 0, n = c.size(); i < n; ++i) {
			const v = c.at(i);
			if (v.isEmpty() && v !== vi) {
				vj = v;
				break;
			}
		}
		const dj  = vj.domain();
		const dcj = vj.solverObject;
		loop_i: for (let i = 0, ni = di.size(); i < ni; ++i) {
			if (dci.isValueHidden(i)) continue;
			vi.assign(di.at(i));  // Tentative assignment to vi
			for (let j = 0, nj = dj.size(); j < nj; ++j) {
				if (dcj.isValueHidden(j)) continue;
				vj.assign(dj.at(j));  // Tentative assignment to vj
				const s = c.satisfactionDegree();
				if (s > this.#solWorstDeg) continue loop_i;  // Tentative assignment to vi was OK -> next tentative assignment.
			}
			dci.hide(i, level);  // It is not a solution when it is 'smaller than or equals'.
		}
		vj.clear();
		vi.clear();
		return !dci.isEmpty();  // Succeeds if the domain di of the future variable vi is not empty.
	}

	// Check for consistency between the current variable and one future variable, and prune elements of the domain that are inconsistent (when there are three unassigned variables in the scope of the constraint).
	#checkForwardConsistency3(level, vi, c) {
		const di  = vi.domain();
		const dci = vi.solverObject;

		let vj = null;
		let vk = null;

		for(let i = 0, n = c.size(); i < n; ++i) {
			let v = c.at(i);
			if (v.isEmpty() && v !== vi) {
				if (vj === null) {
					vj = v;
				} else {
					vk = v;
					break;
				}
			}
		}
		const dj  = vj.domain();
		const dk  = vk.domain();
		const dcj = vj.solverObject;
		const dck = vk.solverObject;

		loop_i: for(let i = 0, ni = di.size(); i < ni; ++i) {
			if (dci.isValueHidden(i)) continue;
			vi.assign(di.at(i));  // Tentative assignment to vi
			for (let j = 0, nj = dj.size(); j < nj; ++j) {
				if (dcj.isValueHidden(j)) continue;
				vj.assign(dj.at(j));  // Tentative assignment to vj
				for (let k = 0, nk = dk.size(); k < nk; ++k) {
					if (dck.isValueHidden(k)) continue;
					vk.assign(dk.at(k));  // Tentative assignment to vk
					const s = c.satisfactionDegree();
					if (s > this.#solWorstDeg) continue loop_i;  // Tentative assignment to vi was OK -> next tentative assignment.
				}
			}
			dci.hide(i, level);  // It is not a solution when it is 'smaller than or equals'.
		}
		vk.clear();
		vj.clear();
		vi.clear();
		return !dci.isEmpty();  // Succeeds if the domain di of the future variable vi is not empty.
	}

	// In the case of polynomial constraints and when there are four or more unassigned variables, all combinations of assignments of unassigned variables are examined and pruned.
	#checkForwardConsistencyN(level, vi, c, emptySize) {
		const di  = vi.domain();
		const dci = vi.solverObject;
		const emp = new Array(emptySize - 1);
		let j = 0;

		for (let i = 0, n = c.size(); i < n; ++i) {
			const v = c.at(i);
			if(v.isEmpty() && v !== vi) emp[j++] = v;
		}
		const indexes = new Array(emp.length);

		loop_i: for (let i = 0, n = di.size(); i < n; ++i) {
			if (dci.isValueHidden(i)) continue;
			vi.assign(di.at(i));  // Tentative assignment to vi
			indexes.fill(0);

			comLoop: while (true) {
				let hidden = false;
				for (let k = 0; k < emp.length; ++k) {
					const dk  = emp[k].domain();
					const dck = emp[k].solverObject;
					if (dck.isValueHidden(indexes[k])) {
						hidden = true;
						break;
					}
					emp[k].assign(dk.at(indexes[k]));
				}
				if (!hidden) {
					const s = c.satisfactionDegree();
					if (s > this.#solWorstDeg) continue loop_i;  // Tentative assignment to vi was OK -> next tentative assignment.
				}
				for (let k = 0; k < emp.length; ++k) {
					indexes[k]++;
					if (indexes[k] < emp[k].domain().size()) break;
					indexes[k] = 0;
					if (k === emp.length - 1) break comLoop;
				}
			}
			dci.hide(i, level);
		}
		for (const v of emp) v.clear();
		vi.clear();
		return !dci.isEmpty();  // Succeeds if the domain di of the future variable vi is not empty.
	}

	// Checks for possible assignment to a future variable from the current variable assignment.
	#checkForward(level, index) {
		for (const v_i of  this.#vars) {
			if (!v_i.isEmpty()) continue;  // If it is a past or present variable.

			const cs = this.#getConstraintsBetween(index, v_i.index());

			for (const c of cs) {
				const emptySize = c.emptyVariableSize();
				if (emptySize === 1) {
					if (!this.#checkForwardConsistency(level, v_i, c)) return false;
				} else if (this.#pruneIntensively) {  // Depends on options
					if (emptySize === 2) {
						if(!this.#checkForwardConsistency2(level, v_i, c)) return false;
					} else if(emptySize === 3) {
						if(!this.#checkForwardConsistency3(level, v_i, c)) return false;
					} else if(emptySize > 3) {
						if(!this.#checkForwardConsistencyN(level, v_i, c, emptySize)) return false;
					}
				}
			}
		}
		return true;
	}

	// Checks to see if the current variable assignment makes the degree of the past variable worse than the current worst degree.
	#checkBackwardConsistency(vc) {
		this.#checkedCons.fill(false);  // Reuse.

		for (let i = 0; i < this.#vars.length; ++i) {  // Find past variables.
			const vi = this.#vars[i];
			if (vi === vc || vi.isEmpty()) continue;  // If it is a future variable or a present variable.
			const cs = this.#getConstraintsBetween(vc.index(), i);

			for (const c of cs) {
				if (this.#checkedCons[c.index()]) continue;  // Because of the possibility of duplication in polynomial constraints
				const s = c.satisfactionDegree();
				if (s !== Constraint.UNDEFINED && s <= this.#solWorstDeg) {  // It is not a solution when it is 'smaller than or equals'.
					return false;
				}
				this.#checkedCons[c.index()] = true;
			}
		}
		return true;
	}

	#refresh() {
		for(let i = 0; i < this.#sequence.length; ++i) {
			const index_vi = this.#sequence[i].index();

			for (let j = i + 1; j < this.#sequence.length; ++j) {
				const vj = this.#sequence[j];
				const cs = this.#getConstraintsBetween(index_vi, vj.index());

				for (const c of cs) {
					const orgVal = vj.value();
					const dj     = vj.domain();
					const dcj    = vj.solverObject;

					for (let k = 0, n = dj.size(); k < n; ++k) {
						if (dcj.isValueHidden(k)) continue;
						vj.assign(dj.at(k));
						if (c.satisfactionDegree() <= this.#solWorstDeg) {
							dcj.hide(k, i);  // Here's a branch pruning!
						}
					}
					vj.assign(orgVal);
				}
			}
		}
	}

	// Returns the index of the smallest domain variable.
	#indexOfVariableWithMRV() {
		let index = 0;
		let size  = Number.MAX_VALUE;

		for (let i = 0; i < this.#vars.length; ++i) {
			const v = this.#vars[i];
			if (!v.isEmpty()) continue;
			const d = v.domain();
			const s = d.size() - v.solverObject.hiddenSize();
			if (s < size) {
				size  = s;
				index = i;
			}
		}
		return index;
	}

	// Performs search one variable at a time.
	#branch(level) {
		let bc = FuzzyForwardChecking.CONTINUE;
		const vc_index = this.#useMRV ? this.#indexOfVariableWithMRV() : level;
		const vc       = this.#vars[vc_index];
		const d        = vc.domain();
		const dc       = vc.solverObject;
		this.#sequence[level] = vc;

		for (let i = 0, n = d.size(); i < n; ++i) {
			if (dc.isValueHidden(i)) continue;
			if ((this._iterLimit && this._iterLimit < this.#iterCount++) || this.#endTime < Date.now()) {
				bc = FuzzyForwardChecking.TERMINATE;  // Search terminated due to restrictions.
				break;
			}
			vc.assign(d.at(i));

			for (const v of this.#vars) v.solverObject.reveal(level);
			if (!this.#checkBackwardConsistency(vc)) continue;
			if (!this.#checkForward(level, vc_index)) continue;

			const nextLevel = level + 1;
			bc = (nextLevel === this.#vars.length - 1) ? this.#branchLast(nextLevel) : this.#branch(nextLevel);
			if (bc === FuzzyForwardChecking.TERMINATE) break;
		}
		if (bc === FuzzyForwardChecking.CONTINUE) {  // When searching back to the parent, undo the branch pruning here.
			for (const v of this.#vars) v.solverObject.reveal(level);
		}
		vc.clear();
		return bc;
	}

	// Performs search on the last variable.
	#branchLast(level) {
		let bc = FuzzyForwardChecking.CONTINUE;
		const vc = this.#vars[this.#useMRV ? this.#indexOfVariableWithMRV() : level];
		const d  = vc.domain();
		const dc = vc.solverObject;
		this.#sequence[level] = vc;

		for (let i = 0, n = d.size(); i < n; ++i) {
			if (dc.isValueHidden(i)) continue;
			if ((this._iterLimit && this._iterLimit < this.#iterCount++) || this.#endTime < Date.now()) {
				bc = FuzzyForwardChecking.TERMINATE;  // Search terminated due to restrictions.
				break;
			}
			vc.assign(d.at(i));

			const deg = this._pro.worstSatisfactionDegree();
			if (deg > this.#solWorstDeg) {  // A new solution is assumed when 'greater than'.
				this.#solWorstDeg = deg;
				this.#sol.setProblem(this._pro);
				bc = FuzzyForwardChecking.TERMINATE;
				if (this._targetDeg !==  null && this._targetDeg <= this.#solWorstDeg) {  // Search ends when target is reached
					break;
				}
				this.#pruneUnaryConstraints();
				this.#refresh();
			}
		}
		vc.clear();
		return bc;
	}

	// Do search.
	exec() {
		this.#endTime   = (this._timeLimit === null) ? Number.MAX_VALUE : (Date.now() + this._timeLimit);
		this.#iterCount = 0;

		for (const v of this.#vars) {
			v.solverObject = new DomainPruner(v.domain().size());  // Generation of domain pruners.
		}
		this._pro.clearAllVariables();
		if (!this.#pruneUnaryConstraints()) return false;  // Since _worstSatisfactionDegree_ has been updated, call this function.

		let success = false;
		while (true) {
			const bc = this.#branch(0);
			if (bc === FuzzyForwardChecking.TERMINATE) {
				if (this._iterLimit && this._iterLimit < this.#iterCount++) {
					if (this._debug) console.log('stop: number of iterations has reached the limit');
					break;
				}
				if (this.#endTime < Date.now()) {
					if (this._debug) console.log('stop: time limit has been reached');
					break;
				}
			}
			if (this.#sol.isEmpty()) {
				break;
			}
			if (this._debug) {
				console.log(`\tfound a solution: ${this.#solWorstDeg}`);
			}
			if (this.foundSolution(this.#sol, this.#solWorstDeg)) {  // Call hook
				success = true;
				break;
			}
			if (this._targetDeg === null) {  // Degree not specified
				success = true;
				this.#solWorstDeg += this.#degInc;  // Find the next solution within the limit.
			} else if (this._targetDeg <= this.#solWorstDeg) {  // The current degree exceeded the specified degree.
				if (this._debug) console.log('stop: current degree is above the target');
				success = true;
				break;
			}
			for (const v of this.#vars) v.solverObject.revealAll();
		}
		this.#sol.apply();
		for (const v of this.#vars) v.solverObject = null;  // Delete branch pruner
		return success;
	}

	/**
	 * Constraint satisfaction degree is set as an achievement goal that serves as a condition for stopping the solver.
	 * The solver stops as successful when the specified degree is reached or exceeded.
	 * The default (unset) is 0.8.
	 * @param rate Degree. null indicates not set.
	 */
	setTargetRate(rate = null) {
		this._targetDeg = rate;
		if (this._targetDeg === null) {
			this.#solWorstDeg = 0;
		} else {
			// Find the worstSatisfactionDegree_ that is slightly smaller than the targetDegree_.
			let e = Number.MIN_VALUE
			this.#solWorstDeg = this._targetDeg - e;
			while (this.#solWorstDeg >= this._targetDeg) {
				e *= 10;
				this.#solWorstDeg = this._targetDeg - e;
			}
		}
	}

	/**
	 * Specify whether to use the minimum-remaining-values (MRV) heuristic.
	 * Use of MRV may increase processing time for some problems.
	 * Default is false.
	 * @param flag Use MRV if true.
	 */
	setUsingMinimumRemainingValuesHeuristics(flag) {
		this.#useMRV = flag;
	}

	/**
	 * If a solution is found and the search continues, it specifies how much the worst constraint satisfaction degree should be increased.
	 * @param degree Increasing constraint satisfaction degree.
	 */
	setIncrementStepOfWorstSatisfactionDegree(degree) {
		this.#degInc = degree;
	}

	/**
	 * Specifies whether or not to intensively prune branches when the problem contains 3- or n-ary constraints.
	 * Depending on the problem, intensive pruning may increase processing time.
	 * Default is false.
	 * @param flag Whether or not to intensively prune branches.
	 */
	setIntensivePruning(flag) {
		this.#pruneIntensively = flag;
	}

}

/**
 * This class implements the forward checking method for fuzzy CSPs that contain only binary constraints.
 * The minimum-remaining-values (MRV) heuristic can also be used by specifying the option.
 *
 * @author Takuto Yanagida
 * @version 2023-03-31
 */

class FuzzyForwardCheckingBc extends Solver {

	static CONTINUE  = 0;
	static TERMINATE = 1;

	#vars;
	#sol = new AssignmentList();
	#relCons;  // Table to cache constraints between two variables.

	#solWorstDeg = 0;  // Degree of existing solutions (no need to find a solution less than this).

	#iterCount;
	#endTime;
	#useMRV = false;
	#degInc = 0;

	/**
	 * Generates the solver given a fuzzy constraint satisfaction problem.
	 * @param p A fuzzy problem.
	 * @param worstSatisfactionDegree Worst satisfaction degree.
	 */
	constructor(p, worstSatisfactionDegree = null) {
		super(p);
		this.#vars = [...this._pro.variables()];
		this.#initializeRelatedConstraintTable();

		this.#solWorstDeg = Math.max(0, p.worstSatisfactionDegree());
		if (worstSatisfactionDegree) {
			this.#solWorstDeg = worstSatisfactionDegree;
		}
	}

	name() {
		return 'Forward Checking for Fuzzy CSPs of Binary Constraints';
	}

	foundSolution() {
		return false;
	}

	// Initializes a table that caches constraints between two variables.
	#initializeRelatedConstraintTable() {
		const temp = [];
		this.#relCons = [];

		for (let j = 0; j < this.#vars.length; ++j) {
			this.#relCons.push(new Array(this.#vars.length));

			for (let i = 0; i < this.#vars.length; i++) {
				if (i < j) {
					this._pro.constraintsBetween(this.#vars[i], this.#vars[j], temp);
					this.#relCons[j][i] = [...temp];
				}
			}
		}
	}

	// Retrieves an array of constraints from a table that caches constraints between two variables.
	#getConstraintsBetween(vi_index, vj_index) {
		if (vi_index < vj_index) {
			return this.#relCons[vj_index][vi_index];
		}
		return this.#relCons[vi_index][vj_index];
	}

	// Check for consistency between the current variable and one future variable, and prune elements of the domain that are inconsistent (when there is one unassigned variable in the scope of the constraint).
	#checkForwardConsistency(level, vi, c) {
		const di  = vi.domain();
		const dci = vi.solverObject;

		for (let i = 0, n = di.size(); i < n; ++i) {
			if (dci.isValueHidden(i)) continue;
			vi.assign(di.at(i));
			if (c.satisfactionDegree() <= this.#solWorstDeg) {  // It is not a solution when it is 'smaller than or equals'.
				dci.hide(i, level);  // Here's a branch pruning!
			}
		}
		vi.clear();
		return !dci.isEmpty();  // Succeeds if the domain di of the future variable vi is not empty.
	}

	// Checks for possible assignment to a future variable from the current variable assignment.
	#checkForward(level, index) {
		for (const v_i of  this.#vars) {
			if (!v_i.isEmpty()) continue;  // If it is a past or present variable.

			const cs = this.#getConstraintsBetween(index, v_i.index());
			for (const c of cs) {
				if (c.size() === 2) {  // If it is a binary constraint.
					if (!this.#checkForwardConsistency(level, v_i, c)) return false;
				}
			}
		}
		return true;
	}

	// Returns the index of the smallest domain variable.
	#indexOfVariableWithMRV() {
		let index = 0;
		let size  = Number.MAX_VALUE;

		for (let i = 0; i < this.#vars.length; ++i) {
			const v = this.#vars[i];
			if (!v.isEmpty()) continue;
			const d = v.domain();
			const s = d.size() - v.solverObject.hiddenSize();
			if (s < size) {
				size  = s;
				index = i;
			}
		}
		return index;
	}

	// Performs search one variable at a time.
	#branch(level) {
		let bc = FuzzyForwardCheckingBc.CONTINUE;
		const vc_index = this.#useMRV ? this.#indexOfVariableWithMRV() : level;
		const vc       = this.#vars[vc_index];
		const d        = vc.domain();
		const dc       = vc.solverObject;

		for (let i = 0, n = d.size(); i < n; ++i) {
			if (dc.isValueHidden(i)) continue;
			if ((this._iterLimit && this._iterLimit < this.#iterCount++) || this.#endTime < Date.now()) {
				bc = FuzzyForwardCheckingBc.TERMINATE;  // Search terminated due to restrictions.
				break;
			}
			vc.assign(d.at(i));

			for (const v of this.#vars) v.solverObject.reveal(level);
			if (!this.#checkForward(level, vc_index)) continue;

			const nextLevel = level + 1;
			bc = (nextLevel === this.#vars.length - 1) ? this.#branchLast(nextLevel) : this.#branch(nextLevel);
			if (bc === FuzzyForwardCheckingBc.TERMINATE) break;
		}
		if (bc === FuzzyForwardCheckingBc.CONTINUE) {  // When searching back to the parent, undo the branch pruning here.
			for (const v of this.#vars) v.solverObject.reveal(level);
		}
		vc.clear();
		return bc;
	}

	// Performs search on the last variable.
	#branchLast(level) {
		let bc = FuzzyForwardCheckingBc.CONTINUE;
		const vc = this.#vars[this.#useMRV ? this.#indexOfVariableWithMRV() : level];
		const d  = vc.domain();
		const dc = vc.solverObject;

		for (let i = 0, n = d.size(); i < n; ++i) {
			if (dc.isValueHidden(i)) continue;
			if ((this._iterLimit && this._iterLimit < this.#iterCount++) || this.#endTime < Date.now()) {
				bc = FuzzyForwardCheckingBc.TERMINATE;  // Search terminated due to restrictions.
				break;
			}
			vc.assign(d.at(i));

			const deg = this._pro.worstSatisfactionDegree();
			if (deg > this.#solWorstDeg) {  // A new solution is assumed when 'greater than'.
				this.#solWorstDeg = deg;
				this.#sol.setProblem(this._pro);
				bc = FuzzyForwardCheckingBc.TERMINATE;  // Search terminated due to restrictions.
				if (this._targetDeg !== null && this._targetDeg <= this.#solWorstDeg) {  // Search ends when target is reached
					break;
				}
			}
		}
		vc.clear();
		return bc;
	}

	// Do search.
	exec() {
		this.#endTime   = (this._timeLimit === null) ? Number.MAX_VALUE : (Date.now() + this._timeLimit);
		this.#iterCount = 0;

		for (const v of this.#vars) {
			v.solverObject = new DomainPruner(v.domain().size());  // Generation of domain pruners.
		}
		this._pro.clearAllVariables();

		const sol = new AssignmentList();

		let success = false;
		while (true) {
			const bc = this.#branch(0);
			if (bc === FuzzyForwardCheckingBc.TERMINATE) {
				if (this._iterLimit && this._iterLimit < this.#iterCount++) {
					if (this._debug) console.log('stop: number of iterations has reached the limit');
					break;
				}
				if (this.#endTime < Date.now()) {
					if (this._debug) console.log('stop: time limit has been reached');
					break;
				}
			}
			if (this.#sol.isEmpty()) {
				break;
			}
			sol.setAssignmentList(this.#sol);
			this.#sol.clear();  // Clear it so that if the solution is not found in the next search, it will be known.

			if (this._debug) {
				console.log(`\tfound a solution: ${this.#solWorstDeg}`);
			}
			if (this.foundSolution(sol, this.#solWorstDeg)) {  // Call hook
				success = true;
				break;
			}
			if (this._targetDeg === null) {  // Degree not specified
				success = true;
				if (this.#solWorstDeg + this.#degInc > 1) break;
				this.#solWorstDeg += ((this.#solWorstDeg + this.#degInc > 1) ? 0 : this.#degInc);  // Find the next solution within the limit.
			} else if (this._targetDeg <= this.#solWorstDeg) {  // The current degree exceeded the specified degree.
				if (this._debug) console.log(`stop: current degree is above the target`);
				success = true;
				break;
			}
			for (const v of this.#vars) v.solverObject.revealAll();
		}
		sol.apply();
		for (const v of this.#vars) v.solverObject = null;  // Delete branch pruner
		return success;
	}

	/**
	 * Constraint satisfaction degree is set as an achievement goal that serves as a condition for stopping the solver.
	 * The solver stops as successful when the specified degree is reached or exceeded.
	 * The default (unset) is 0.8.
	 * @param rate Degree. null indicates not set.
	 */
	setTargetRate(rate = null) {
		this._targetDeg = rate;
		if (this._targetDeg === null) {
			this.#solWorstDeg = 0;
		} else {
			// Find the worstSatisfactionDegree_ that is slightly smaller than the targetDegree_.
			let e = Number.MIN_VALUE
			this.#solWorstDeg = this._targetDeg - e;
			while (this.#solWorstDeg >= this._targetDeg) {
				e *= 10;
				this.#solWorstDeg = this._targetDeg - e;
			}
		}
	}

	/**
	 * Specify whether to use the minimum-remaining-values (MRV) heuristic.
	 * Use of MRV may increase processing time for some problems.
	 * Default is false.
	 * @param flag Use MRV if true.
	 */
	setUsingMinimumRemainingValuesHeuristics(flag) {
		this.#useMRV = flag;
	}

	/**
	 * If a solution is found and the search continues, it specifies how much the worst constraint satisfaction degree should be increased.
	 * @param degree Increasing constraint satisfaction degree.
	 */
	setIncrementStepOfWorstSatisfactionDegree(degree) {
		this.#degInc = degree;
	}

}

/**
 * This class implements fuzzy GENET.
 * CSPs and FCSPs (but only Binary (F)CSPs) is supported.
 *
 * @author Takuto Yanagida
 * @version 2023-03-31
 */

class FuzzyGENET extends Solver {

	static nextInt(max) {
		return Math.floor(Math.random() * Math.floor(max));
	}

	#clusters = [];
	#connections;

	constructor(p, worstSatisfactionDegree = 1) {
		super(p);
		if (!this.#createNetwork(worstSatisfactionDegree)) {
			throw new Exception();
		}
	}

	name() {
		return 'Fuzzy GENET';
	}

	#createNetwork(worstDeg) {
		if (this._debug) console.log('network creation start');
		const cons = [];

		for (const v of this._pro.variables()) {
			if (v.domain().size() === 0) return false;
			this.#clusters.push(new FuzzyGENET.Cluster(v));
		}
		for (const c of this._pro.constraints()) {
			if (c.size() === 1) {  // In the case of unary constraints.
				const v  = c.at(0);
				const cl = this.#clusters[c.at(0).index()];

				for (let i = 0; i < cl.size(); ++i) {
					const origVal = v.value();  // Save the value.
					v.assign(cl.get(i)._value);

					if (c.satisfactionDegree() <= worstDeg) {
						cons.push(new FuzzyGENET.Connection(c, cl.get(i)));
					}
					v.assign(origVal);  // Restore the value.
				}
			} else {  // In the case of binary constraints.
				const v1   = c.at(0);
				const v2   = c.at(1);
				const cl_f = this.#clusters[c.at(0).index()];
				const cl_s = this.#clusters[c.at(1).index()];

				for (let i = 0; i < cl_f.size(); ++i) {
					const origVal1 = v1.value();  // Save the value.
					v1.assign(cl_f.get(i)._value);

					for (let j = 0; j < cl_s.size(); ++j) {
						const origVal2 = v2.value();  // Save the value.
						v2.assign(cl_s.get(j)._value);

						if (c.satisfactionDegree() <= worstDeg) {
							cons.push(new FuzzyGENET.Connection(c, cl_f.get(i), cl_s.get(j)));
						}
						v2.assign(origVal2);  // Restore the value.
					}
					v1.assign(origVal1);  // Restore the value.
				}
			}
		}
		for (const cl of this.#clusters) {
			for (const n of cl._neurons) n.lockConnections();
		}
		this.#connections = cons;
		if (this._debug) console.log('network creation complete');
		return true;
	}

	#shuffle(is) {
		for (let i = is.length; i > 1; --i) {
			const j    = GENET.nextInt(i);
			const temp = is[i - 1];
			is[i - 1] = is[j];
			is[j]     = temp;
		}
		return is;
	}

	exec() {
		const endTime = (this._timeLimit === null) ? Number.MAX_VALUE : (Date.now() + this._timeLimit);
		let iterCount = 0;

		const sol   = new AssignmentList();
		const order = [];
		for (let i = 0; i < this.#clusters.length; i++) {
			order.push(i);
		}

		let cur     = this._pro.worstSatisfactionDegree();
		let success = false;

		while (true) {
			if (this._iterLimit && this._iterLimit < iterCount++) {  // Failure if repeated a specified number
				if (this._debug) console.log('stop: number of iterations has reached the limit');
				break;
			}
			if (endTime < Date.now()) {  // Failure if time limit is exceeded
				if (this._debug) console.log('stop: time limit has been reached');
				break;
			}

			let modified = false;
			for (const i of this.#shuffle(order)) {
				if (this.#clusters[i].setActivityMaximumInput()) {
					modified = true;  // Turn on the node with the largest input in each cluster
				}
			}
			if (!modified) {  // When the local minimum solution is reached.
				for (const con of this.#connections) {
					con.refreshWeight();  // Update weights for all connections
				}
				continue;
			} else {
				for (const clu of this.#clusters) {
					clu.applyToVariable();  // Apply to variable
				}
				const d = this._pro.worstSatisfactionDegree();
				if (cur < d) {  // If it's a better assignment than ever, save it.
					cur = d;
					if (this._debug) console.log(`worst satisfaction degree: ${d}`);
					sol.setProblem(this._pro);
					if (this.foundSolution(sol, d)) {  // Call hook
						success = true;
						break;
					}
					if (this._targetDeg === null) {  // Satisfaction degree is not specified.
						success = true;
					} else if (this._targetDeg <= cur) {  // Satisfaction degree is specified.
						if (this._debug) console.log('stop: current degree is above the target');
						success = true;
						break;
					}
				}
			}
		}
		sol.apply();
		return success;
	}

}

{

	class Cluster {

		static nextInt(max) {
			return Math.floor(Math.random() * Math.floor(max));
		}

		#var;
		#index;
		#maxNeurons = [];
		_neurons;

		constructor(v) {
			this.#var = v;

			const d = v.domain();
			this._neurons = [];

			for (let i = 0; i < d.size(); ++i) {
				this._neurons.push(new Neuron(d.at(i)));
			}
			this.#setActivity(Cluster.nextInt(this._neurons.length));
		}

		#setActivity(index) {
			for (const n of this._neurons) {
				n._isActive = false;
			}
			this._neurons[index]._isActive = true;
			this.#index = index;
		}

		applyToVariable() {
			this.#var.assign(this._neurons[this.#index]._value);
		}

		get(index) {
			return this._neurons[index];
		}

		neurons() {
			return this._neurons;
		}

		// Turn on the node with the largest input.
		setActivityMaximumInput() {
			this.#maxNeurons.length = 0;

			let max       = Number.NEGATIVE_INFINITY;
			let alreadyOn = false;

			for (let i = 0; i < this._neurons.length; ++i) {
				const input = this._neurons[i].getInput();

				if (max <= input) {
					if (max < input) {
						max = input;
						this.#maxNeurons.length = 0;
						alreadyOn = false;
					}
					this.#maxNeurons.push(i);
					if (this.#index === i) {
						alreadyOn = true;
					}
				}
			}
			if (alreadyOn || this.#maxNeurons.length === 0) {
				return false;
			}
			this.#setActivity(this.#maxNeurons[Cluster.nextInt(this.#maxNeurons.length)]);
			return true;
		}

		size() {
			return this._neurons.length;
		}

	}
	FuzzyGENET.Cluster = Cluster;

	class Connection {

		#c
		#first;
		#second;
		_weight;  // Direct reference (read) allowed.

		// Order of neurons must be the same as the order of variables that the constraint has.
		constructor(c, first, second = null) {
			this._weight = c.satisfactionDegree() - 1;
			this.#c      = c;

			this.#first = first;
			this.#first.addConnection(this);
			this.#second = second;
			if (this.#second !== null) {
				this.#second.addConnection(this);
			}
		}

		getNeuron(self) {
			if (self === this.#first)  return this.#second;
			if (self === this.#second) return this.#first;
			return null;
		}

		refreshWeight() {
			if (!this.#first._isActive || (this.#second !== null && !this.#second._isActive)) {
				return;
			}
			if (this.#c.size() === 1) {
				this._weight += (this.#c.fuzzyRelation().satisfactionDegree(this.#first._value) - 1);
			} else {
				this._weight += (this.#c.fuzzyRelation().satisfactionDegree(this.#first._value, this.#second._value) - 1);
			}
		}

	}
	FuzzyGENET.Connection = Connection;

	class Neuron {

		#conTemp = [];
		#connections;
		_value;  // Direct reference (read) allowed.
		_isActive = false;  // Direct reference (read, write) allowed.

		constructor(value) {
			this._value = value;
		}

		addConnection(c) {
			this.#conTemp.push(c);
		}

		lockConnections() {
			this.#connections = [...this.#conTemp];
			this.#conTemp     = null;  // No longer being used.
		}

		getInput() {
			let ret = 0;
			for (const c of this.#connections) {
				const n = c.getNeuron(this);  // If n is null, then the unary constraint.
				ret += c._weight * ((n === null || n._isActive) ? 1 : 0);
			}
			return ret;
		}

	}
	FuzzyGENET.Neuron = Neuron;

}

/**
 * A class that implements the flexible local changes method.
 *
 * @author Takuto Yanagida
 * @version 2023-03-31
 */

class FlexibleLocalChanges extends Solver {

	static #setPlusSet(s1, s2) {
		const sn = new Set(s1);
		for (const v of s2) sn.add(v);
		return sn;
	}

	static #setMinusSet(s1, s2) {
		const sn = new Set(s1);
		for (const v of s2) sn.delete(v);
		return sn;
	}

	static #setPlusElement(s, e) {
		const sn = new Set(s);
		sn.add(e);
		return sn;
	}

	static #setMinusElement(s, e) {
		const sn = new Set(s);
		sn.delete(e);
		return sn;
	}

	#lt;
	#lb;

	#iterCount;
	#endTime;
	#globalReturn;

	constructor(p) {
		super(p);
		this.#computeHighestAndLowestConsistencyDegree();
	}

	name() {
		return 'Flexible Local Changes';
	}

	#choose(x2, cr) {
		const res = new Map();

		for (const c of cr) {
			if (!c.isDefined()) {
				continue;
			}
			for (let i = 0; i < c.size(); ++i) {
				const v = c.at(i);
				if (!res.has(v)) {
					res.set(v, 1);
				} else {
					res.set(v, res.get(v) + 1);
				}
			}
		}
		const vs = [...x2];
		vs.sort((o1, o2) => {
			let res1 = 0;
			let res2 = 0;
			if (res.has(o1)) res1 = res.get(o1);
			if (res.has(o2)) res2 = res.get(o2);

			if (res1 < res2) return 1;
			if (res1 > res2) return -1;
			return 0;
		});

		const ret = new Set();

		for (const v of vs) {
			let remain = false;
			for (const c of cr) {
				if (c.isDefined()) {
					remain = true;
					break;
				}
			}
			if (!remain) break;
			v.clear();
			ret.add(v);
		}
		return ret;
	}

	#computeHighestAndLowestConsistencyDegree() {
		let low  = 1;
		let high = 0;

		for (const v of this._pro.variables()) {
			for (let i = 0; i < v.size(); ++i) {
				const c = v.at(i);

				const l = c.lowestConsistencyDegree();
				const h = c.highestConsistencyDegree();
				if (l < low)  low  = l;
				if (h > high) high = h;
			}
		}
		this.#lb = low;
		this.#lt = high;
	}

	#flcRepair(X1, X2, xi, consX1xi, consX12, cr, rc) {
		const X3p = this.#choose(X2, cr);
		const X1p = FlexibleLocalChanges.#setPlusElement(X1, xi);
		const X2p = FlexibleLocalChanges.#setMinusSet(X2, X3p);
		return this.#flcVariables(X1p, X2p, X3p, consX1xi, Math.min(consX12, consX1xi), rc);
	}

	#flcVariable(X1, X2, xi, consX1, consX12, rc) {
		let bestCons = this.#lb;
		if (xi.domain().size() === 0) {
			return bestCons;
		}
		let bestX2  = AssignmentList.fromVariables(X2);
		let bestDij = xi.domain().at(0);

		const x2Store = AssignmentList.fromVariables(X2);

		for (let j = 0; j < xi.domain().size() && bestCons < consX12; ++j) {
			const dij = xi.domain().at(j);
			xi.assign(dij);
			const consX1_xi = Math.min(consX1, this.#testX1(X1, xi, bestCons, rc));

			if (consX1_xi > Math.max(bestCons, rc)) {
				const crNew = new Set();
				const consX12_xi = Math.min(Math.min(consX1_xi, consX12), this.#testX12(X1, X2, xi, consX1_xi, consX12, crNew));

				if (consX12_xi > bestCons) {
					bestCons = consX12_xi;
					bestDij  = dij;
					bestX2   = AssignmentList.fromVariables(X2);
				}
				if (crNew.size) {
					const repairCons = this.#flcRepair(X1, X2, xi, consX1_xi, consX12, crNew, Math.max(rc, bestCons));
					if (this.#globalReturn !== -1) {
						return bestCons;
					}
					if (repairCons > bestCons) {
						bestCons = repairCons;
						bestDij  = dij;
						bestX2   = AssignmentList.fromVariables(X2);
					}
					x2Store.apply();
				}
			}
		}
		bestX2.apply();
		xi.assign(bestDij);
		return bestCons;
	}

	#flcVariables(X1, X2, X3, consX1, consX12, rc) {
		if (this._debug) {
			console.log(`X1 ${X1.size}, X2' ${X2.size}, X3' ${X3.size}`);
		}
		if (this._targetDeg !== null && this._targetDeg <= this._pro.worstSatisfactionDegree()) {  // Success if the degree improves from specified
			if (this._debug) console.log('stop: current degree is above the target');
			this.#globalReturn = 1;
			return consX12;
		}
		if (this._iterLimit && this._iterLimit < this.#iterCount++) {  // Failure if repeated a specified number
			if (this._debug) console.log('stop: number of iterations has reached the limit');
			this.#globalReturn = 0;
			return consX12;
		}
		if (this.#endTime < Date.now()) {  // Failure if time limit is exceeded
			if (this._debug) console.log('stop: time limit has been reached');
			this.#globalReturn = 0;
			return consX12;
		}
		if (X3.size === 0) {
			return consX12;
		}
		const xi        = X3.values().next().value;
		const consX12xi = this.#flcVariable(X1, X2, xi, consX1, consX12, rc);

		if (this.#globalReturn !== -1) {
			return consX12;
		}
		if (consX12xi < rc) {
			return this.#lb;
		}
		X2 = FlexibleLocalChanges.#setPlusElement(X2, xi);
		X3 = FlexibleLocalChanges.#setMinusElement(X3, xi);
		return this.#flcVariables(X1, X2, X3, consX1, consX12xi, rc);
	}

	#initTest(X, cr) {
		const cs = new Set();
		for (const v of X) {
			for (let i = 0; i < v.size(); ++i) {
				cs.add(v.at(i));  // All variables in X have been assigned.
			}
		}
		let ret = 1;
		for (const c of cs) {
			const sd = c.satisfactionDegree();
			if (sd === Constraint.UNDEFINED) continue;
			if (sd < ret) ret = sd;
		}
		for (const c of this._pro.constraints()) {
			const cd = c.lowestConsistencyDegree();
			if (cd < this.#lt) cr.add(c);
		}
		return ret;
	}

	#testX1(X1, xi, bestCons, rc) {
		let cd = 1;
		const cs   = new Set();
		const temp = [];

		for (const v of X1) {
			this._pro.constraintsBetween(v, xi, temp);
			for (const c of temp) cs.add(c);
		}
		for (const c of cs) {
			const d = c.satisfactionDegree();
			if (d === Constraint.UNDEFINED) continue;
			if (d < cd) cd = d;
			if (cd <= bestCons || cd <= rc) return cd;  // If it is determined that a better solution than the current solution cannot be obtained
		}
		return cd;
	}

	#testX12(X1, X2, xi, consX1xi, consX12, cr) {
		let csd = 1.0;
		const cs   = new Set();
		const temp = [];

		for (const v of X1) {
			this._pro.constraintsBetween(v, xi, temp);
			for (const c of temp) cs.add(c);
		}
		for (const v of X2) {
			this._pro.constraintsBetween(v, xi, temp);
			for (const c of temp) cs.add(c);
		}
		for (const c of cs) {
			const sd = c.satisfactionDegree();
			if (sd === Constraint.UNDEFINED) continue;
			if (sd < csd) csd = sd;
		}
		for (const c of cs) {
			const sd = c.satisfactionDegree();
			if (sd === Constraint.UNDEFINED) continue;
			if (sd < consX1xi || sd < consX12) cr.add(c);
		}
		return csd;
	}

	exec() {
		this.#endTime      = (this._timeLimit === null) ? Number.MAX_VALUE : (Date.now() + this._timeLimit);
		this.#iterCount    = 0;
		this.#globalReturn = -1;

		const wsd = this._pro.worstSatisfactionDegree();
		if (this._pro.emptyVariableSize() === 0) {
			this._pro.clearAllVariables();
		}
		const X1 = new Set();
		const X2 = new Set();  // Currently assigned variables.
		const X3 = new Set();  // Currently unassigned variables.
		for (const v of this._pro.variables()) {
			(!v.isEmpty() ? X2 : X3).add(v);
		}

		const cr       = new Set();
		const initCons = this.#initTest(X2, cr);
		let rc;
		let initSol = null;

		if (X3.size === 0) {
			rc      = initCons;
			initSol = AssignmentList.fromVariables(X2);
		} else {
			rc = this.#lb;
		}
		const X3p = FlexibleLocalChanges.#setPlusSet(this.#choose(X2, cr), X3);
		const X2p = FlexibleLocalChanges.#setMinusSet(X2, X3p);
		let result = this.#flcVariables(X1, X2p, X3p, this.#lt, this.#lt, rc);
		if (result < rc) {
			if (initSol !== null) {
				initSol.apply();
			}
		}
		result = this._pro.worstSatisfactionDegree();
		return result > wsd && result > 0 && (this.#globalReturn !== 0 || this._targetDeg === null);
	}

}

/**
 * A class that implements the flexible local changes method.
 * The implementation is optimized by converting recursive calls to loops.
 *
 * @author Takuto Yanagida
 * @version 2023-03-31
 */

class FlexibleLocalChangesEx extends Solver {

	static #setPlusSet(s1, s2) {
		const sn = new Set(s1);
		for (const v of s2) sn.add(v);
		return sn;
	}

	static #setMinusSet(s1, s2) {
		const sn = new Set(s1);
		for (const v of s2) sn.delete(v);
		return sn;
	}

	static #setPlusElement(s, e) {
		const sn = new Set(s);
		sn.add(e);
		return sn;
	}

	#lt;
	#lb;

	#iterCount;
	#endTime;
	#globalReturn;

	constructor(p) {
		super(p);
		this.#computeHighestAndLowestConsistencyDegree();
	}

	name() {
		return 'Flexible Local Changes Ex';
	}

	#choose(x2, cr) {
		const res = new Map();

		for (const c of cr) {
			if (!c.isDefined()) {
				continue;
			}
			for (let i = 0; i < c.size(); ++i) {
				const v = c.at(i);
				if (!res.has(v)) {
					res.set(v, 1);
				} else {
					res.set(v, res.get(v) + 1);
				}
			}
		}
		const vs = [...x2];
		vs.sort((o1, o2) => {
			let res1 = 0;
			let res2 = 0;
			if (res.has(o1)) res1 = res.get(o1);
			if (res.has(o2)) res2 = res.get(o2);

			if (res1 < res2) return 1;
			if (res1 > res2) return -1;
			return 0;
		});

		const ret = new Set();

		for (const v of vs) {
			let remain = false;
			for (const c of cr) {
				if (c.isDefined()) {
					remain = true;
					break;
				}
			}
			if (!remain) break;
			v.clear();
			ret.add(v);
		}
		return ret;
	}

	#computeHighestAndLowestConsistencyDegree() {
		let low  = 1;
		let high = 0;

		for (const v of this._pro.variables()) {
			for (let i = 0; i < v.size(); ++i) {
				const c = v.at(i);

				const l = c.lowestConsistencyDegree();
				const h = c.highestConsistencyDegree();
				if (l < low)  low  = l;
				if (h > high) high = h;
			}
		}
		this.#lb = low;
		this.#lt = high;
	}

	#flcRepair(X1, X2, xi, consX1xi, consX12, cr, rc) {
		const X3p = this.#choose(X2, cr);
		const X1p = FlexibleLocalChangesEx.#setPlusElement(X1, xi);
		const X2p = FlexibleLocalChangesEx.#setMinusSet(X2, X3p);
		return this.#flcVariables(X1p, X2p, X3p, consX1xi, Math.min(consX12, consX1xi), rc);
	}

	#flcVariable(X1, X2, xi, consX1, consX12, rc) {
		let bestCons = this.#lb;
		if (xi.domain().size() === 0) {
			return bestCons;
		}
		let bestX2  = AssignmentList.fromVariables(X2);
		let bestDij = xi.domain().at(0);

		const x2Store = AssignmentList.fromVariables(X2);

		for (let j = 0; j < xi.domain().size() && bestCons < consX12; ++j) {
			const dij = xi.domain().at(j);
			xi.assign(dij);
			const consX1_xi = Math.min(consX1, this.#testX1(X1, xi, bestCons, rc));

			if (consX1_xi > Math.max(bestCons, rc)) {
				const crNew = new Set();
				const consX12_xi = Math.min(Math.min(consX1_xi, consX12), this.#testX12(X1, X2, xi, consX1_xi, consX12, crNew));

				if (consX12_xi > bestCons) {
					bestCons = consX12_xi;
					bestDij  = dij;
					bestX2   = AssignmentList.fromVariables(X2);
				}
				if (crNew.size) {
					const repairCons = this.#flcRepair(X1, X2, xi, consX1_xi, consX12, crNew, Math.max(rc, bestCons));
					if (this.#globalReturn !== -1) {
						return bestCons;
					}
					if (repairCons > bestCons) {
						bestCons = repairCons;
						bestDij  = dij;
						bestX2   = AssignmentList.fromVariables(X2);
					}
					x2Store.apply();
				}
			}
		}
		bestX2.apply();
		xi.assign(bestDij);
		return bestCons;
	}

	#flcVariables(X1, X2, X3, consX1, consX12, rc) {
		X2 = new Set(X2);  // Clone
		X3 = new Set(X3);  // Clone

		while (true) {
			if (this._debug) {
				console.log(`X1 ${X1.size}, X2' ${X2.size}, X3' ${X3.size}`);
			}
			if (this._targetDeg !== null && this._targetDeg <= this._pro.worstSatisfactionDegree()) {  // Success if the degree improves from specified
				if (this._debug) console.log('stop: current degree is above the target');
				this.#globalReturn = 1;
				return consX12;
			}
			if (this._iterLimit && this._iterLimit < this.#iterCount++) {  // Failure if repeated a specified number
				if (this._debug) console.log('stop: number of iterations has reached the limit');
				this.#globalReturn = 0;
				return consX12;
			}
			if (this.#endTime < Date.now()) {  // Failure if time limit is exceeded
				if (this._debug) console.log('stop: time limit has been reached');
				this.#globalReturn = 0;
				return consX12;
			}
			if (X3.size === 0) {
				return consX12;
			}
			const xi        = X3.values().next().value;
			const consX12xi = this.#flcVariable(X1, X2, xi, consX1, consX12, rc);

			if (this.#globalReturn !== -1) {
				return consX12;
			}
			if (consX12xi < rc) {
				return this.#lb;
			}
			X2.add(xi);
			X3.delete(xi);
			consX12 = consX12xi;
		}
	}

	#initTest(X, cr) {
		const cs = new Set();
		for (const v of X) {
			for (let i = 0; i < v.size(); ++i) {
				cs.add(v.at(i));  // All variables in X have been assigned.
			}
		}
		let ret = 1;
		for (const c of cs) {
			const sd = c.satisfactionDegree();
			if (sd === Constraint.UNDEFINED) continue;
			if (sd < ret) ret = sd;
		}
		for (const c of this._pro.constraints()) {
			const cd = c.lowestConsistencyDegree();
			if (cd < this.#lt) cr.add(c);
		}
		return ret;
	}

	#testX1(X1, xi, bestCons, rc) {
		let cd = 1;
		const cs   = new Set();
		const temp = [];

		for (const v of X1) {
			this._pro.constraintsBetween(v, xi, temp);
			for (const c of temp) cs.add(c);
		}
		for (const c of cs) {
			const d = c.satisfactionDegree();
			if (d === Constraint.UNDEFINED) continue;
			if (d < cd) cd = d;
			if (cd <= bestCons || cd <= rc) return cd;  // If it is determined that a better solution than the current solution cannot be obtained
		}
		return cd;
	}

	#testX12(X1, X2, xi, consX1xi, consX12, cr) {
		let csd = 1.0;
		const cs   = new Set();
		const temp = [];

		for (const v of X1) {
			this._pro.constraintsBetween(v, xi, temp);
			for (const c of temp) cs.add(c);
		}
		for (const v of X2) {
			this._pro.constraintsBetween(v, xi, temp);
			for (const c of temp) cs.add(c);
		}
		for (const c of cs) {
			const sd = c.satisfactionDegree();
			if (sd === Constraint.UNDEFINED) continue;
			if (sd < csd) csd = sd;
		}
		for (const c of cs) {
			const sd = c.satisfactionDegree();
			if (sd === Constraint.UNDEFINED) continue;
			if (sd < consX1xi || sd < consX12) cr.add(c);
		}
		return csd;
	}

	exec() {
		this.#endTime      = (this._timeLimit === null) ? Number.MAX_VALUE : (Date.now() + this._timeLimit);
		this.#iterCount    = 0;
		this.#globalReturn = -1;

		const wsd = this._pro.worstSatisfactionDegree();
		if (this._pro.emptyVariableSize() === 0) {
			this._pro.clearAllVariables();
		}
		const X1 = new Set();
		const X2 = new Set();  // Currently assigned variables.
		const X3 = new Set();  // Currently unassigned variables.
		for (const v of this._pro.variables()) {
			(!v.isEmpty() ? X2 : X3).add(v);
		}

		const cr       = new Set();
		const initCons = this.#initTest(X2, cr);
		let rc;
		let initSol = null;

		if (X3.size === 0) {
			rc      = initCons;
			initSol = AssignmentList.fromVariables(X2);
		} else {
			rc = this.#lb;
		}
		const X3p = FlexibleLocalChangesEx.#setPlusSet(this.#choose(X2, cr), X3);
		const X2p = FlexibleLocalChangesEx.#setMinusSet(X2, X3p);
		let result = this.#flcVariables(X1, X2p, X3p, this.#lt, this.#lt, rc);
		if (result < rc) {
			if (initSol !== null) {
				initSol.apply();
			}
		}
		result = this._pro.worstSatisfactionDegree();
		return result > wsd && result > 0 && (this.#globalReturn !== 0 || this._targetDeg === null);
	}

}

/**
 * This class implements the SRS algorithm.
 *
 * @author Takuto Yanagida
 * @version 2023-03-31
 */

class SRS3 extends Solver {

	// Threshold for adopting a candidate assignment at repair time (should be 0 if strictly following SRS 3)
	static REPAIR_THRESHOLD = 0;

	#closedList          = new Set();
	#openList            = new Set();  // LinkedHashSet is used in the original implementation.
	#nodes               = [];
	#neighborConstraints = [];  // Cache

	#c_stars = new Set();  // ArrayList is used in the original implementation.

	#iterCount;
	#endTime;
	#isRandom = true;

	constructor(p) {
		super(p);
		for (const c of this._pro.constraints()) {
			this.#nodes.push(new CrispSRS3.TreeNode(c));
			this.#neighborConstraints.push(null);
		}
	}

	name() {
		return 'SRS 3';
	}

	foundSolution(solution, worstDegree) {
		return false;
	}

	#getNeighborConstraints(c) {
		const index = c.index();

		if (this.#neighborConstraints[index] === null) {
			this.#neighborConstraints[index] = c.neighbors([]);
		}
		return this.#neighborConstraints[index];
	}

	#repair(c0) {
		if (this._debug) console.log('repair');

		const candidates = new AssignmentList();
		const minDeg0    = c0.satisfactionDegree();  // Target c0 should certainly be an improvement over this.
		const min        = this._pro.worstSatisfactionDegree();  // Lower bound of neighborhood constraints.
		let maxDeg0 = c0.satisfactionDegree();  // Satisfaction degree of target c0 for the most improvement so far.

		// If a candidate satisfying the condition is stronger than the previous candidates,
		// it is replaced, and if no candidate is found until the end, it fails.
		for (let i = 0; i < c0.size(); ++i) {
			const v     = c0.at(i);
			const v_val = v.value();  // Save the value
			const v_c   = v.constraints();
			const v_d   = v.domain();

			out: for (let j = 0; j < v_d.size(); ++j) {
				const d = v_d.at(j);
				if (v_val === d) continue;
				v.assign(d);
				const deg0 = c0.satisfactionDegree();
				// If target c0 cannot be improved, the assignment is rejected.
				if (minDeg0 > deg0 || maxDeg0 - deg0 > SRS3.REPAIR_THRESHOLD) continue;

				for (const c of v_c) {
					if (c === c0) continue;
					const deg = c.satisfactionDegree();
					// If one of the neighborhood constraints c is less than or equal to the worst, the assignment is rejected.
					if (deg !== Constraint.UNDEFINED && deg < min) continue out;
				}
				if (deg0 > maxDeg0) {
					maxDeg0 = deg0;
					candidates.clear();
				}
				candidates.addVariable(v, d);
			}
			v.assign(v_val);  // Restore the value
		}
		if (candidates.size() > 0) {
			const e = this.#isRandom ? candidates.arbitraryAssignment() : candidates.get(0);
			console.log(e);
			e.apply();
			if (this._debug) console.log('\t' + e);
			return true;
		}
		return false;
	}

	#shrink(node) {
		if (this._debug) console.log('shrink');

		let removeCStar = false;
		while (true) {
			node = node.parent();
			if (this.#c_stars.delete(node)) {
				removeCStar = true;
				break;
			}
			if (!this.#repair(node.parent().getObject())) break;
		}
		const temp = [];
		node.getDescendants(temp);  // temp contains node.

		for (const n of temp) {
			n.clear();  // Prepare for reuse
			this.#openList.delete(n);
			this.#closedList.delete(n);
		}

		if (!removeCStar) {
			this.#openList.add(node);
		}
	}

	#spread(node) {
		if (this._debug) console.log('spread');
		this.#closedList.add(node);

		for (const c of this.#getNeighborConstraints(node.getObject())) {
			const cn = this.#nodes[c.index()];

			if (!this.#closedList.has(cn) && !this.#openList.has(cn)) {  // For constraints that are not included in Open or Closed.
				node.add(cn);
				this.#openList.add(cn);
			}
		}
	}

	#srs() {
		if (this._debug) console.log('srs');

		const wsdcs = [];
		this._pro.constraintsWithWorstSatisfactionDegree(wsdcs);
		for (const c of wsdcs) {
			const cn = this.#nodes[c.index()];
			cn.setParent(null);
			this.#c_stars.add(cn);
		}
		this.#closedList.clear();
		this.#openList.clear();
		for (const n of this.#c_stars) {
			this.#openList.add(n);
		}

		while (this.#c_stars.size && this.#openList.size) {
			if (this._iterLimit && this._iterLimit < iterCount++) {  // Failure if repeated a specified number
				if (this._debug) console.log('stop: number of iterations has reached the limit');
				return false;
			}
			if (this.#endTime < Date.now()) {  // Failure if time limit is exceeded
				if (this._debug) console.log('stop: time limit has been reached');
				return false;
			}

			const node = this.#openList.values().next().value;
			this.#openList.delete(node);

			if (this.#repair(node.getObject())) {
				if (this.#c_stars.delete(node)) continue;  // If the repaired node is included in C* (to be deleted)
				if (this.#repair(node.parent().getObject())) {
					this.#shrink(node);  // When its improvement leads to the improvement of its parents
					continue;
				}
			}
			this.#spread(node);
		}
		return true;
	}

	exec() {
		this.#endTime   = (this._timeLimit === null) ? Number.MAX_VALUE : (Date.now() + this._timeLimit);
		this.#iterCount = 0;
		if (this._targetDeg && this._targetDeg <= this._pro.worstSatisfactionDegree()) {
			return true;
		}
		const sol = new AssignmentList();

		let success = false;
		while (true) {
			const ret = this.#srs();
			if (!ret || this.#c_stars.size) {
				break;
			}
			const solutionWorstDeg = this._pro.worstSatisfactionDegree();
			if (this._debug) {
				console.log(`\tfound a solution: ${solutionWorstDeg}\t${this._targetDeg}`);
			}
			sol.setProblem(this._pro);

			if (this.foundSolution(sol, solutionWorstDeg)) {  // Call hook
				success = true;
				break;
			}
			if (this._targetDeg === null) {  // Satisfaction degree is not specified
				success = true;
			} else if (this._targetDeg <= solutionWorstDeg) {  // The current degree exceeded the specified degree.
				if (this._debug) console.log('stop: current degree is above the target');
				success = true;
				break;
			}
		}
		return success;
	}

	/**
	 * Sets the randomness of the algorithm.
	 * Enabling randomness reduces the risk of falling into a local solution, but makes the solution unrepeatable.
	 * @param flag If true, randomness is enabled.
	 */
	setRandomness(flag) {
		this.#isRandom = flag;
	}

}

{

	class TreeNode {

		#children = [];
		#parent;
		#obj;

		constructor(obj) {
			this.#obj = obj;
		}

		add(tn) {
			tn.#parent = this;
			this.#children.push(tn);
		}

		clear() {
			for (const tn of this.#children) {
				tn.#parent = null;
			}
			this.#children.length = 0;
		}

		getDescendants(tns) {
			tns.push(this);

			for (const tn of this.#children) {
				tn.getDescendants(tns);
			}
		}

		getObject() {
			return this.#obj;
		}

		parent() {
			return this.#parent;
		}

		setParent(p) {
			this.#parent = p;
		}

	}
	CrispSRS3.TreeNode = TreeNode;

}

/**
 * This class implements the SRS algorithm with PF.
 *
 * @author Takuto Yanagida
 * @version 2023-03-31
 */

class SRS3_PF extends SRS3 {

	constructor(p) {
		super(p);
	}

	name() {
		return 'SRS 3 + PF';
	}

	exec() {
		let deg = 0;
		let uvs = 0;
		if (this._debug) {
			deg = this._pro.worstSatisfactionDegree();
			uvs = this._pro.emptyVariableSize();
		}
		const at = new AssignmentList();
		at.setProblem(this._pro);

		const res = super.exec();

		if (res) {
			PostStabilization.apply(this._pro, at);
		}
		if (this._debug) {
			console.log(`result: ${res ? 'success' : 'failure'}`);
			console.log(`satisfaction degree: ${deg} -> ${this._pro.worstSatisfactionDegree()}`);
			console.log(`uUnassigned size: ${uvs} -> ${this._pro.emptyVariableSize()}`);
		}
		return res;
	}

}


/**
 * The class implements AC-3, one of the arc consistency algorithms.
 *
 * @author Takuto Yanagida
 * @version 2023-03-26
 */

class AC3 {

	static #checkConsistency(c, v_j) {
		const d_j = v_j.domain();

		for (let i = 0; i < d_j.size(); ++i) {  // Is there a partner that satisfies the constraint?
			v_j.assign(d_j.at(i));

			if (c.isSatisfied() === 1) {  // It exists!
				return true;  // Current assignment of v_i is consistent.
			}
		}
		return false;
	}

	static #reviseDomain(p, v_i, v_j) {
		const val_i = v_i.value();
		const val_j = v_j.value();  // Save the value.
		const d_i   = v_i.domain();
		const temp  = [];

		const cs = [];
		p.constraintsBetween(v_i, v_j, cs);

		vals: for (let i = 0; i < d_i.size(); ++i) {
			v_i.assign(d_i.at(i));

			for (let k = 0; k < cs.size(); ++k) {
				const c = cs.get(k);
				if (c.size() !== 2) continue;  // Check the next constraint
				if (!AC3.#checkConsistency(c, v_j)) continue vals;   // Since there is no partner satisfying the constraint, check the next value.
			}
			temp.push(d_i.at(i));
		}
		v_i.assign(val_i); v_j.assign(val_j);  // Restore the value.

		if (temp.length !== d_i.size()) {
			const nd = p.createDomain({ values: temp });
			v_i.setDomain(nd);
			console.log(d_i.size() + ' -> ' + nd.size());
			return true;
		}
		return false;
	}

	static apply(p) {
		const cs = [];

		for (const c of p.constraints()) {
			if (c.size() === 2) cs.add(c);
		}
		while (!cs.isEmpty()) {
			const c   = cs.remove(cs.size() - 1);
			const v_k = c.at(0);
			const v_m = c.at(1);

			if (AC3.#reviseDomain(p, v_k, v_m)) {
				for (const c1 of p.constraints()) {
					if (c1.size() === 2 && c1.at(1) === v_k && c1.at(0) !== v_m) {
						cs.add(0, c1);
					}
				}
			}
		}
	}

}

/**
 * Utility class that performs node consistency.
 *
 * @author Takuto Yanagida
 * @version 2023-03-26
 */

class NodeConsistency {

	/**
	 * Guarantees consistency of fuzzy unary constraints. The domain of each variable is replaced as needed.
	 * Deletes elements from domains that do not meet the specified worst satisfaction degree.
	 * @param p A problem.
	 * @param threshold Worst satisfaction degree.
	 * @return True if there is no empty domain.
	 */
	static apply(p, threshold) {
		for (const v of p.variables()) {
			const d       = v.domain();
			const origVal = v.value();  // Save the value.
			const elms    = [];

			for (let i = 0; i < v.size(); ++i) {
				const c = v.at(i);
				if (c.size() !== 1) continue;

				for (let j = 0; j < d.size(); ++j) {
					v.assign(d.at(j));

					if (c.satisfactionDegree() >= threshold) {
						elms.add(val);
					}
				}
				p.removeConstraint(c);
			}
			v.assign(origVal);  // Restore the value.
			if (elms.isEmpty()) {
				return false;
			}
			v.setDomain(p.createDomain({ values: elms }));
		}
		return true;
	}

	/**
	 * Guarantees consistency of crisp unary constraints. The domain of each variable is replaced as needed.
	 * It cannot be applied to crisp views of fuzzy constraint satisfaction problems because it changes the structure of the constraint graph.
	 * @param p A crisp problem.
	 * @return True if there is no empty domain.
	 */
	static apply(p) {
		for (const v of p.variables()) {
			const d       = v.domain();
			const origVal = v.value();  // Save the value.
			const elms    = [];

			for (let i = 0; i < v.size(); ++i) {
				const c = v.at(i);
				if (c.size() !== 1) continue;

				for (let j = 0; j < d.size(); ++j) {
					v.assign(d.at(j));

					if (c.isSatisfied() === 1) {
						elms.add(val);
					}
				}
				p.removeConstraint(c);
			}
			v.assign(origVal);  // Restore the value.
			if (elms.isEmpty()) {
				return false;
			}
			v.setDomain(p.createDomain({ values: elms }));
		}
		return true;
	}

}

/**
 * Class of post-stabilization.
 *
 * @author Takuto Yanagida
 * @version 2023-03-26
 */

class PostStabilization {

	static apply(p, orig) {
		console.log('start post-stabilization');

		let stabilized;
		let count = 0;

		do {
			console.log('post-stabilization: count ' + count++);

			stabilized = false;
			let C_min = p.worstSatisfactionDegree();

			for (let i = 0; i < p.variableSize(); ++i) {
				const v = p.variableAt(i);
				const org = v.value();

				const a = orig.get(i);
				if (org === a.value()) continue;

				a.apply();  // Try to assign the original.
				if (p.worstSatisfactionDegree() >= C_min) {
					stabilized = true;
				} else {
					v.assign(org);  // Restore.
				}
			}
		} while (stabilized);

		console.log('finish post-stabilization');
		return true;
	}

}

